<template>
  <div class="ui-color-picker">
    <ColorPicker
      :value="value || '#cccccc'"
      @input="$emit('input', $event.hex8)"
    />
  </div>
</template>

<script>
import { Slider } from 'vue-color';

export default {
  name: 'UiColorPicker',

  components: {
    ColorPicker: Slider,
  },

  props: {
    value: {
      type: String,
      required: false,
      default: null,
    },
  },
};
</script>