<template>
  <div class="ui-data-table">
    <table
      cellspacing="0"
      cellpadding="0"
    >
      <thead>
        <tr>
          <th
            v-for="(column, i) in sanitizedColumns"
            :key="i"
            :title="column.title"
            :class="{ '--sticky': column.sticky }"
            :style="{ textAlign: column.align || 'left' }"
          >
            <div v-if="$slots['header-column'] || $scopedSlots['header-column']">
              <slot
                name="header-column"
                :column="column"
              ></slot>
            </div>
            <span v-else>{{ column.title }}</span>
          </th>
        </tr>
      </thead>

      <tbody>
        <tr
          v-for="(row, i) in rows"
          :key="i"
          @click="$emit('click-row', data[i])"
          v-bind="rowAttrs[i]"
        >
          <td
            v-for="(column, j) in sanitizedColumns"
            :key="`cell-${i}-${j}`"
            :class="{ '--sticky': column.sticky }"
            :style="{ textAlign: column.align || 'left' }"
            @click="$emit('click-cell', row[j])"
          >
            <TableCell
              class="data-table-cell"
              :column="column"
              :record="data[i]"
              :value="row[j]"
            />
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { getProperty } from '../../helpers';
import TableCell from './TableCell.vue';

export default {
  name: 'UiDataTable',
  components: { TableCell },

  props: {
    data: {
      type: Array,
      required: false,
      default: () => [],
    },

    columns: {
      type: Array,
      required: false,
      default: () => [],
    },

    rowAttrs: {
      type: Array,
      required: false,
      default: () => [],
    },
  },

  data() {
    return {};
  },

  computed: {
    sanitizedColumns() {
      return this.columns.map((column) => Object.assign({ props: {} }, column));
    },

    rows() {
      if (!Array.isArray(this.data)) {
        return [];
      }

      return this.data.map((record) => {
        let row = [];

        this.columns.forEach((column, i) => {
          let valueSource = column.src;
          let cellValue = null;

          if (typeof valueSource == 'function') {
            cellValue = valueSource(record);
          } else if (
            typeof valueSource == 'string' &&
            valueSource.substr(0, 1) == '$'
          ) {
            if (valueSource == '$') {
              cellValue = record;
            } else {
              cellValue = getProperty(record, valueSource.substr(2));
            }
          }

          if (typeof column.filter == 'function') {
            cellValue = column.filter(cellValue);
          }

          row[i] = cellValue;
        });

        return row;
      });
    },
  },

  // methods: {
  //   highlightIndex(rowIndex) {
  //     this.$set(this.rowClassnames, rowIndex, '--highlighted');
  //     setTimeout(() => this.$set(this.rowClassnames, rowIndex, null), 2000);
  //     return true;
  //   },
  // },
};
</script>

<style lang="scss">
@keyframes highlight {
  0% {
    background: #ff8;
  }
  100% {
    background: none;
  }
}

.ui-data-table {
  overflow: auto;
  position: relative;

  table {
    width: 100%;
    max-width: 100%;

    tr:nth-child(even) {
      background-color: rgba(0, 0, 0, 0.025);
    }

    td,
    th {
      max-width: 300px;

      .data-table-cell {
        display: flex;
        align-items: center;

        padding: 0 var(--ui-breathe);
        overflow: auto;
        min-height: 32px;
        max-height: 120px;
      }
    }

    tbody {
      // Grid / Borders
      td {
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        border-right: 1px solid rgba(0, 0, 0, 0.1);
      }

      tr:hover {
        // color de hover de las filas.  No puede ser semitransparente porque
        // mostraria el contenido detras de columnas "sticky"
        background-color: rgb(255, 255, 211);
      }

      tr.--highlighted {
        td {
          animation: highlight 2s;
        }
      }

      tr.--selected {
        background-color: var(--ui-color-selected);
      }
    }

    thead {
      font-weight: bold;
      font-family: var(--ui-font-secondary);

      th {
        background-color: #fff;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        padding: 4px var(--ui-breathe);
        text-align: left;

        position: sticky;
        top: 0;
        z-index: 2;

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }

    .--sticky {
      position: sticky;
      left: 0;
      z-index: 3;
    }

    th.--sticky {
      z-index: 4;
    }
  }
}
</style>
