<template>
  <component
    :is="definition.component"
    v-bind="blockProps"
    v-on="computedListeners"
    v-model="computedModel"
  ></component>
</template>

<script>
import { getProperty, setProperty, interpolate } from '../../functions';

export default {
  name: 'cms-block-editor-component',

  inject: {
    cmsSettings: {
      default: null,
    },
  },

  props: {
    definition: {
      type: Object,
      required: true,
    },

    value: {
      type: Object,
      required: true,
    },

    page: {
      type: Object,
      required: false,
      default: () => ({}),
    },
  },

  data() {
    return {
      block: null,
      options: {},
      interpolableData: null,
    };
  },

  watch: {
    value: {
      immediate: true,
      deep: true,
      handler(newValue) {
        this.block = JSON.parse(JSON.stringify(newValue));
        this.interpolableData = {
          block: this.block,
          page: this.page,

          $settings:
            this.cmsSettings && this.cmsSettings[this.block.component]
              ? this.cmsSettings[this.block.component]
              : null,
        };
      },
    },
  },

  computed: {
    blockProps() {
      return interpolate(this.definition.props, this.interpolableData);
    },

    computedModel: {
      get() {
        return this.definition['v-model']
          ? getProperty(this.interpolableData, this.definition['v-model'])
          : this.block;
      },

      set(newValue) {
        if (this.definition['v-model']) {
          setProperty(
            this.interpolableData,
            this.definition['v-model'],
            newValue
          );
          this.block = this.interpolableData.block;
        } else {
          this.block = Object.assign({}, this.block, newValue);
        }

        this.$emit('input', JSON.parse(JSON.stringify(this.block)));
      },
    },

    // all passed listeners, except input
    computedListeners() {
      return Object.assign({}, this.$listeners, { input: undefined });
    },
  },
};
</script>