<template>
  <UiField
    class="ui-input"
    :label="type != 'checkbox' && type != 'button' ? label : ''"
    :required="required"
    :message="message"
    :disabled="disabled"
  >
    <slot name="default">
      <template v-if="type == 'textarea'">
        <textarea
          style="width: 99%"
          class="ui-native"
          v-model="innerValue"
          @input="doInput"
          v-bind="$attrs"
        ></textarea>
      </template>
      <template v-else-if="type == 'select'">
        <template v-if="$attrs.native">
          <UiSelect
            v-model="innerValue"
            @input="doInput"
            v-bind="$attrs"
          ></UiSelect>
        </template>
        <template v-else>
          <UiSelectChecklist
            v-if="$attrs.multiple"
            v-model="innerValue"
            @input="doInput"
            v-bind="$attrs"
          ></UiSelectChecklist>
          <UiInputRadio
            v-else
            v-model="innerValue"
            @input="doInput"
            v-bind="$attrs"
          ></UiInputRadio>
        </template>
      </template>
      <template v-else-if="type == 'date'">
        <UiInputDate
          v-model="innerValue"
          @input="doInput"
          v-bind="$attrs"
        ></UiInputDate>
      </template>
      <template v-else-if="type == 'timestamp'">
        <UiInputDate
          format="timestamp"
          v-model="innerValue"
          @input="doInput"
          v-bind="$attrs"
        ></UiInputDate>
      </template>
      <template v-else-if="type == 'file'">
        <UiInputFile
          v-model="innerValue"
          @input="doInput"
          v-bind="$attrs"
        ></UiInputFile>
      </template>
      <template v-else-if="type == 'json'">
        <UiInputJson
          v-model="innerValue"
          @input="doInput"
          v-bind="$attrs"
        ></UiInputJson>
      </template>
      <template v-else-if="type == 'checkbox'">
        <UiInputCheckbox
          v-model="innerValue"
          @input="doInput"
          :label="label"
          v-bind="$attrs"
        ></UiInputCheckbox>
      </template>
      <template v-else-if="type == 'button'">
        <button
          type="button"
          @click="$emit('click', $event)"
          v-bind="$attrs"
          :class="['ui-button', '--main', $attrs.theme]"
        >{{label || '....'}}</button>
      </template>
      <template v-else>
        <input
          class="ui-native"
          :type="type"
          v-model="innerValue"
          @input="doInput"
          v-bind="$attrs"
        />
      </template>
    </slot>
  </UiField>
</template>

<script>
import UiField from '../UiField/UiField.vue';
import UiSelect from '../UiSelect/UiSelect.vue';
import UiInputCheckbox from '../UiInputCheckbox/UiInputCheckbox.vue';
import UiInputDate from '../UiInputDate/UiInputDate.vue';
import UiInputFile from '../UiInputFile/UiInputFile.vue';
import UiSelectChecklist from '../UiSelectChecklist/UiSelectChecklist.vue';
import UiInputJson from '../UiInputJson/UiInputJson.vue';
import UiInputRadio from '../UiInputRadio/UiInputRadio.vue';

export default {
  name: 'UiInput',

  components: {
    UiField,
    UiSelect,
    UiInputCheckbox,
    UiInputDate,
    UiInputFile,
    UiSelectChecklist,
    UiInputJson,
    UiInputRadio,
  },

  props: {
    type: {
      type: String,
      required: false,
      default: 'text',
      validator: (value) =>
        [
          'text',
          'number',
          'range',
          'textarea',
          'select',
          'checkbox',
          'date',
          'timestamp',
          'csv',
          'file',
          'password',
          'button',
          'json',
        ].includes(value),
    },

    value: {
      required: false,
      default: null,
    },

    // Field props
    label: {
      type: String,
      required: false,
      default: '',
    },

    required: {
      type: Boolean,
      default: false,
    },

    message: {
      type: [String, Array],
      required: false,
      default: () => [],
    },

    disabled: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      innerValue: null,
    };
  },

  watch: {
    value: {
      immediate: true,
      handler(value) {
        this.innerValue = JSON.parse(JSON.stringify(value)); //clone
      },
    },
  },

  methods: {
    doInput() {
      this.$emit('input', JSON.parse(JSON.stringify(this.innerValue)));
    },
  },
};
</script>