<template>
  <div class="ui-input-rating ui-row ui-noselect --tight">
    <UiIcon
      v-for="n in parseInt(max)"
      :key="n"
      color="var(--ui-color-primary)"
      class="ui-clickable"
      :value="n<=value ? 'mdi:star' : 'mdi:star-outline'"
      @click="$emit('input', n == 1 && value == n ? 0 : n)"
    />
    <div>{{ value }} / {{ max }}</div>
  </div>
</template>

<script>
import UiIcon from '../UiIcon/UiIcon.vue';

export default {
  name: 'UiInputRating',
  components: {
    UiIcon,
  },
  props: {
    value: {
      type: [Number, String],
      required: false,
      default: 0,
    },

    max: {
      type: [Number, String],
      required: false,
      default: 5,
    },
  },
};
</script>