<template>
  <div
    class="ui-input-date ui-inset ui-noselect"
    :class="{'--mobile':isMobile, '--empty':!date, '--disabled':disabled}"
  >
    <template v-if="isMobile">
      <input
        class="main-input"
        type="text"
      />

      <UiItem
        class="ui-input-date-face --nowrap"
        :text="displayedDate"
        @click="onClickFace"
      >
        <template #right>
          <UiIcon
            class="icon-right ui-padded"
            value="g:today"
          />
        </template>
      </UiItem>
    </template>

    <UiPopover
      v-else
      :open.sync="isOpen"
      autoload
    >
      <template #trigger>
        <UiItem
          class="ui-input-date-face"
          :text="displayedDate"
        >
          <template #right>
            <UiIcon
              class="icon-right ui-padded"
              value="g:today"
            />
          </template>
        </UiItem>
      </template>

      <template #contents>
        <div class="ui-input-date-popup">
          <input
            class="main-input"
            type="text"
          />

          <footer>
            <button
              v-if="showTodayButton"
              type="button"
              class="ui-button"
              @click="setToday()"
            >{{ $t("DatePicker.Today") }}</button>

            <button
              type="button"
              class="ui-button --main"
              @click="save()"
              v-if="time"
            >{{ $t("DatePicker.OK") }}</button>

            <button
              type="button"
              class="ui-button --cancel"
              @click="clear()"
            >{{ $t("DatePicker.Clear") }}</button>
          </footer>
        </div>
      </template>
    </UiPopover>
  </div>
</template>

<script>
import 'flatpickr/dist/flatpickr.min.css';
import 'flatpickr/dist/themes/airbnb.css';
import Flatpickr from 'flatpickr';

import { isMobile } from '../../helpers';
import useI18n from '../../../i18n/mixins/useI18n.js';

import UiIcon from '../UiIcon/UiIcon.vue';
import UiItem from '../UiItem/UiItem.vue';
import UiPopover from '../UiPopover/UiPopover.vue';

export default {
  name: 'DateInternal',
  mixins: [useI18n],

  components: {
    UiIcon,
    UiItem,
    UiPopover,
  },

  props: {
    value: {
      type: Date,
      required: false,
      default: null,
    },

    /**
     * Valor truthy, o "24" para mostrar el reloj en formato 24 horas
     */
    time: {
      required: false,
      default: null,
    },

    placeholder: {
      type: String,
      required: false,
      default: null,
    },

    showTodayButton: {
      type: Boolean,
      required: false,
      default: true,
    },

    disabled: {
      type: Boolean,
      required: false,
      default: false,
    },

    disableBefore: {
      type: [Date, String, Number],
      required: false,
      default: null,
    },

    min: {
      type: Date,
      required: false,
      default: null,
    },

    max: {
      type: Date,
      required: false,
      default: null,
    },
  },

  data() {
    return {
      date: null, // valor representado como un objeto Date
      instance: null,
      config: {},
      isOpen: false,
      isMobile: isMobile(),
    };
  },

  computed: {
    displayedDate() {
      if (!this.date) {
        return this.placeholder;
      }

      let retval = this.$date(this.date, 'day');
      if (this.time) {
        retval += ' ' + this.$date(this.date, 'time');
      }
      return retval;
    },

    sensitiveValues() {
      return [this.time, this.disableBefore, this.min, this.max];
    },
  },

  watch: {
    value(newValue) {
      this.setDate(newValue);
    },

    sensitiveValues() {
      this.initializeInstance();
    },
  },

  mounted() {
    this.date = this.value ? new Date(this.value) : null;
    this.initializeInstance();
  },

  methods: {
    onClickFace() {
      this.instance.open();
    },

    initializeInstance() {
      let flatpickrOptions = {
        enableTime: this.time != null,
        time_24hr: this.time == '24',
        inline: !this.isMobile,
        onChange: this.onInstanceChange,
        minDate: this.min,
        maxDate: this.max,
      };

      if (this.disableBefore) {
        let dateBefore =
          typeof this.disableBefore == 'object'
            ? this.disableBefore
            : new Date(this.disableBefore * 1000);

        dateBefore.setHours(0, 0, 0, 0);

        flatpickrOptions.disable = [(date) => date < dateBefore];
      }

      let fpElement = this.$el.querySelector('.main-input');
      if (fpElement) {
        this.instance = Flatpickr(fpElement, flatpickrOptions);
        this.instance.setDate(this.date);
      } else {
        // El elemento no esta renderizado aun (!?).   Esperar un poco
        setTimeout(() => {
          let fpElement = this.$el.querySelector('.main-input');
          this.instance = Flatpickr(fpElement, flatpickrOptions);
          this.instance.setDate(this.date);
        }, 250);
      }
    },

    setDate(newValue) {
      if (newValue == this.date) {
        return;
      }

      if (
        newValue != null &&
        this.date != null &&
        newValue.getTime() == this.date.getTime()
      ) {
        return;
      }

      if (!newValue) {
        this.date = null;
        this.instance.clear();
        return;
      }

      this.date = new Date(newValue);
      this.instance.setDate(newValue);
    },

    close() {
      this.isOpen = false;
    },

    save() {
      let value = this.instance.selectedDates.length
        ? this.instance.selectedDates[0]
        : null;

      if (this.date == value) {
        this.close();
        return;
      }

      this.date = value ? new Date(value) : null;
      this.$emit('input', this.date);
      this.close();
    },

    onInstanceChange() {
      // si no hay selector de tiempo, guardar al seleccionar un dia
      if (this.isMobile || !this.time) {
        this.save();
      }
    },

    setToday() {
      this.setDate(new Date());
      if (!this.time) {
        this.save();
      }
    },

    clear() {
      this.setDate(null);
      this.$emit('input', null);
      this.close();
    },
  },

  i18n: {
    en: {
      'DatePicker.OK': 'OK',
      'DatePicker.Today': 'Today',
      'DatePicker.Clear': 'Clear',
    },

    de: {
      'DatePicker.Today': 'Heute',
    },

    es: {
      'DatePicker.OK': 'OK',
      'DatePicker.Today': 'Hoy',
      'DatePicker.Clear': 'Limpiar',
    },

    fr: {
      'DatePicker.Today': "Aujourd'hui",
    },

    it: {
      'DatePicker.Today': 'Oggi',
    },
  },
};
</script>

<style lang="scss">
.ui-input-date {
  display: inline-block;
  position: relative;

  .ui-input-date-face {
    .icon-right {
      opacity: 0.7;
    }
  }

  // Hide the main input tag
  .main-input {
    z-index: -10;
    position: absolute;
    top: 0;
    left: 0;
    border: 0;
    background: transparent;

    // visibility: hidden; // ESTO IMPIDE instance.open() en IOS !!!
  }

  .flatpickr-calendar {
    box-shadow: none;
  }

  .ui-input-date-popup {
    footer {
      text-align: center;
      padding: var(--ui-padding-vertical) 0;
      background-color: #fcfcfc;
      button {
        margin: 0 1em;
      }
    }
  }

  // Placeholder when empty (!!! is styling .item-body with is a UiItem internal)
  &.--empty {
    .ui-input-date-face .item-body {
      opacity: 0.5;
    }
  }

  &.--mobile {
    .ui-input-date-popup {
      display: block;
      visibility: hidden;
    }
  }

  &.--disabled {
    pointer-events: none;
    opacity: 0.5;
  }
}
</style>