<template>
  <div id="app">
    <h1><strong>Phi</strong>UX</h1>

    <!-- <CmsPageEditor v-model="testPage" /> -->
    <CmsPage v-model="testPage" />

  </div>
</template>

<script>
/*eslint-disable*/
import UiVideoRecorder from '/packages/ui/components/UiVideoRecorder/UiVideoRecorder.vue';
import { CmsPage, CmsPageEditor } from '../../packages/cms';
import { UiVideo } from '../../packages/ui';

import story1 from '../samples/story1.js';

export default {
  components: {
    UiVideo,
    UiVideoRecorder,
    CmsPage,
    CmsPageEditor,
  },

  data() {
    return {
      testPage: story1,
      isPreview: false,
      videoData: null,

      stream: null,
      recorder: null,
      finalSrc: null,

      chapters: [
        {
          name: 'Segundo 1',
          start: 0,
          end: 1000,
        },
        {
          name: 'Segundo 2',
          start: 1000,
          end: 2000,
        },
        {
          name: 'Segundo 3',
          start: 2000,
          end: 3000,
        },
        {
          name: 'Overlap',
          start: 1500,
          end: 2500,
        },
      ],
    };
  },

  methods: {
    onTimeupdate() {
      console.log('cms.vue onTimeupdate');
    },

    onUpdateChapters(evt) {
      console.log('cms.vue onUpdateChapters', evt);
    },

    async start() {
      this.finalSrc = null;
      await this.getStream();
      this.startRecording();
    },

    stop() {
      this.stopRecording();

      if (!this.stream) {
        return;
      }

      this.stream.getTracks().forEach((track) => track.stop());
      this.stream = null;
    },

    getStream() {
      return navigator.mediaDevices
        .getUserMedia({
          video: true,
          audio: true,
        })
        .then((stream) => (this.stream = stream));
    },

    startRecording() {
      this.recorder = new MediaRecorder(this.stream);
      this.recorder.start();
    },

    stopRecording() {
      this.recorder.ondataavailable = (event) => {
        this.finalSrc = URL.createObjectURL(event.data);
      };

      this.recorder.stop();
    },
  },
};
</script>

<style>
:root {
  --ui-color-primary: #41b883;
  --ui-color-secondary: #35495e;
}

#app {
  max-width: 900px;
  margin: auto;
}

.header {
  text-align: center;
}

.icon-stack {
  position: relative;
  display: inline-block;
}

.icon-stack > :first-child {
  position: absolute;
  top: 0;
  left: 0;
}
</style>