<template>
  <div class="schema-field-picker">
    <select
      v-show="!showCustom"
      v-model="innerValue"
      @change="onSelectChange"
      class="ui-native"
    >
      <option :value="null">{{ label }}</option>

      <optgroup
        v-if="customConditions"
        label="Condiciones custom"
      >
        <option
          v-for="(opDef, opName) in customConditions"
          :key="opName"
          :value="opName"
          v-text="opDef.text"
        ></option>
      </optgroup>

      <optgroup
        v-if="primitiveFields.length"
        label="Atributos"
      >
        <option
          v-for="field in primitiveFields"
          :key="field.src"
          :value="field.src"
          v-text="field.title || field.src"
        ></option>
      </optgroup>

      <optgroup label="Grupo">
        <option value="or">Cualquiera</option>
        <option value="and">Todas</option>
      </optgroup>

      <option value="custom">Custom ...</option>
    </select>

    <div
      class="custom-launcher"
      v-if="showCustom"
    >
      <p>Condicion custom ...</p>
      <button
        type="button"
        @click="showCustom = false"
      >Cerrar</button>
    </div>

  </div>
</template>

<script>
import { schemaGetPrimitiveFields } from './functions/schema';

export default {
  name: 'ConditionLauncher',

  props: {
    schema: {
      type: Object,
      required: false,
      default: null,
    },

    label: {
      type: String,
      required: false,
      default: '-- Agregar condición --',
    },

    customConditions: {
      type: Object,
      required: false,
      default: null,
    },
  },

  data() {
    return {
      innerValue: null,
      showCustom: false,
    };
  },

  methods: {
    onSelectChange() {
      let opName = this.innerValue;
      this.innerValue = null; // resetear el <select>

      if (opName == 'custom') {
        this.showCustom = true;
        return;
      }

      if (
        this.customConditions &&
        typeof this.customConditions[opName] != 'undefined'
      ) {
        this.$emit('input', {
          field: null,
          op: opName,
          args: null,
        });
        return;
      }

      if (['or', 'and'].includes(opName)) {
        this.$emit('input', {
          field: null,
          op: opName,
          args: [],
        });
        return;
      }

      // Determine the default op for the selected field
      let selectedField = this.primitiveFields.find((i) => i.src == opName);
      let op = null;

      if (selectedField) {
        if (selectedField.enum) {
          op = 'enum.anyOf';
        } else {
          switch (selectedField.type) {
            case 'string':
              op = 'string.eq';
              break;
            case 'number':
              op = 'number.eq';
              break;
            case 'boolean':
              op = 'boolean.true';
              break;
            case 'date':
              op = 'date.eq';
              break;
            case 'array':
              op = 'array.hasAny';
              break;
          }
        }

        this.$emit('input', {
          field: selectedField.src,
          op,
          args: null,
        });
      }
    },
  },

  computed: {
    primitiveFields() {
      return schemaGetPrimitiveFields(this.schema);
    },
  },
};
</script>