<template>
  <div
    class="ui-input-wedge"
    :class="{'--open': isOpen, '--closed': !isOpen, '--not-empty': !!innerValue}"
  >
    <UiIcon
      class="wedge-icon ui-clickable"
      :value="icon"
      @click="toggle"
    ></UiIcon>
    <div class="wedge-drawer">
      <input
        type="text"
        v-model="innerValue"
        @keyup.enter="emitInput"
        @keyup.esc="cancel"
      >
    </div>
  </div>
</template>

<script>
import UiIcon from '../UiIcon/UiIcon.vue';

export default {
  name: 'UiInputWedge',
  components: { UiIcon },

  props: {
    value: {
      required: false,
      default: null,
    },

    icon: {
      type: String,
      required: false,
      default: 'mdi-variable',
    },

    badgeColor: {
      type: String,
      required: false,
      default: '',
    },
  },

  data() {
    return {
      innerValue: null,
      isOpen: false,
    };
  },

  watch: {
    value: {
      immediate: true,
      handler(newValue) {
        this.innerValue = JSON.parse(JSON.stringify(newValue));
      },
    },
  },

  methods: {
    open() {
      if (this.isOpen) {
        return;
      }
      this.isOpen = true;
      this.$emit('update:open', true);
      this.focus();
    },

    close() {
      if (!this.isOpen) {
        return;
      }
      this.isOpen = false;
      this.$emit('update:open', false);
    },

    toggle() {
      return this.isOpen ? this.close() : this.open();
    },

    emitInput() {
      this.$emit('input', JSON.parse(JSON.stringify(this.innerValue)));
      this.close();
    },

    cancel() {
      this.innerValue = JSON.parse(JSON.stringify(this.value));
      this.close();
    },

    focus() {
      return this.$nextTick(() => this.$el.querySelector('input').select());
      // return this.$nextTick(() => this.$el.querySelector('input').focus());
      // return this.$el.querySelector('input').focus();
    },
  },
};
</script>

<style lang="scss">
.ui-input-wedge {
  display: inline-flex;

  // // Badge
  // &.--not-empty {
  //   .wedge-icon {

  //     position: relative;
