  //       height: 10px;
  //       border-radius: 50%;
  //       background-color: var(--ui-color-success);

  //       position: absolute;
  //       top: 0;
  //       right: 0;
  //       margin: 4px;

  //       opacity: 0.85;
  //     }
  //   }
  // }

  .wedge-drawer {
    flex: 1;
    overflow: hidden;

    transition: max-width var(--ui-duration-snap);
  }

  &.--closed {
    .wedge-drawer {
      max-width: 0;
    }
  }

  &.--open {
    .wedge-drawer {
      max-width: 120px;
    }
  }
}
</style>