<template>
  <div class="ui-audio-recorder">
    <template v-if="!isAudioSupported || !permissionGranted">
      <input
        type="file"
        accept="audio/*"
        capture
      />
    </template>
    <template v-else>
      <UiAudioVisualizer
        v-if="isRecording && stream"
        :stream="stream"
      ></UiAudioVisualizer>

      <UiItem
        v-show="!isRecording"
        class="ui-clickable ui-outset"
        @click="start"
        text="Grabar"
        icon="mdi:record"
      ></UiItem>

      <UiItem
        v-show="isRecording"
        class="ui-clickable ui-outset"
        @click="stop"
        text="Detener"
        icon="mdi:record-rec"
        icon-color="red"
      ></UiItem>

      <template v-if="audioUrl">
        <UiAudio :url="audioUrl"></UiAudio>

        <button
          type="button"
          class="ui-native"
          @click="accept"
        >Aceptar</button>

        <button
          type="button"
          class="ui-native"
          @click="clear"
        >Descartar</button>
      </template>
    </template>
  </div>
</template>

<script>
import { UiAudio } from '../UiAudio';
import { UiAudioVisualizer } from '../UiAudioVisualizer';
import { UiItem } from '../UiItem';

export default {
  name: 'UiAudioRecorder',

  components: {
    UiAudio,
    UiAudioVisualizer,
    UiItem,
  },

  props: {
    mimeType: {
      type: String,
      required: false,
      default: 'audio/webm',
    },
  },

  data() {
    return {
      permissionGranted: true,

      stream: null,
      mediaRecorder: null,
      chunks: [],
      blob: null,

      isRecording: false,
      audioUrl: null,
    };
  },

  computed: {
    isAudioSupported() {
      // return navigator && navigator.mediaDevices
      return typeof MediaRecorder != 'undefined';
    },
  },

  methods: {
    async initializeRecorder() {
      if (this.mediaRecorder) {
        return this.mediaRecorder;
      }

      try {
        this.stream = await navigator.mediaDevices.getUserMedia({
          audio: true,
          video: false,
        });
      } catch (err) {
        this.permissionGranted = false;
        throw err;
      }

      this.mediaRecorder = new MediaRecorder(this.stream, {
        mimeType: this.mimeType,
      });

      this.mediaRecorder.addEventListener('dataavailable', (e) => {
        if (e.data.size > 0) {
          this.chunks.push(e.data);
        }
      });

      this.mediaRecorder.addEventListener('stop', (/*event*/) => {
        this.blob = new Blob(this.chunks);
        this.audioUrl = URL.createObjectURL(this.blob);
      });

      return this.mediaRecorder;
    },

    start() {
      this.initializeRecorder()
        .then(() => {
          this.mediaRecorder.start();
          this.isRecording = true;
          this.$emit('start', { stream: this.stream });
        })
        .catch((err) => this.$emit('error', err));
    },

    async stop() {
      if (!this.mediaRecorder) {
        return;
      }

      this.mediaRecorder.stop();
      this.isRecording = false;
      this.$emit('stop');
    },

    clear() {
      this.stream.getTracks().forEach((track) => track.stop());

      this.stream = null;
      this.mediaRecorder = null;
      this.chunks = [];
      this.blob = null;
      this.audioUrl = null;
    },

    accept() {
      this.$emit('done', { blob: this.blob });
      this.clear();
    },
  },
};
</script>