export default {
  getPageValidationRules(page) {
    if (!page) {
      return [];
    }

    let rules = [];

    // Atributo "validation" en la pagina
    if (Array.isArray(page.validation) && page.validation.length > 0) {
      rules = rules.concat(page.validation);
    }

    // Atributo "validation" en los bloques
    page.blocks
      .filter((b) => Array.isArray(b.validation) && b.validation.length > 0)
      .forEach((b) => (rules = rules.concat(b.validation)));


    // Bloques asociados a v-model con la prop "required"
    page.blocks
      .filter((block) => block['v-model'] && block.props && block.props.required)
      .forEach(requiredBlock => {
        let props = requiredBlock.props || {};
        let label = props.label || props.title || requiredBlock['v-model'];

        rules.push({
          text: `'${label}' es obligatorio`,
          rule: { [requiredBlock['v-model']]: { '&nempty': true } }
        });
      });


    return rules;
  },

  getDataSchema(page) {
    let retval = {
      type: 'object',
      properties: {}
    };

    if (!page || !page.blocks || !page.blocks.length) {
      return retval;
    }

    page.blocks
      .filter((b) => !!b['v-model'])
      .forEach((block) => {
        let props = block.props || {};

        let field = {
          name: block['v-model'],
          title: props.label || props.title || block['v-model'],
          label: props.label || props.title || block['v-model'],
          text: props.label || props.title || block['v-model'],
          secondary: props.label || props.title || block['v-model'],
          type: null,
          props: null,
        };

        switch (block.component) {
          case 'CmsInput':
            switch (props.type) {
              case 'select':
                if (props.multiple) {
                  field.type = 'array';
                  field.items = {
                    type: 'string',
                    enum: props.options || []
                  };
                } else {
                  field.type = 'string';
                  field.enum = props.options || [];
                }
                break;

              case 'text':
              case 'textarea':
                field.type = 'text';
                break;

              case 'number':
                field.type = 'number';
                break;

              case 'checkbox':
                field.type = 'boolean';
                break;
            }
            break;

          case 'CmsInputSelect':
          case 'InputSelect':
            if (props.multiple) {
              field.type = 'array';
              field.items = {
                type: 'string',
                enum: props.options || []
              };
            } else {
              field.type = 'string';
              field.enum = props.options || [];
            }
            break;

          case 'CmsInputText':
          case 'CmsInputTextarea':
          case 'InputText':
          case 'InputTextarea':
            field.type = 'text';
            break;

          case 'CmsInputNumber':
          case 'InputNumber':
            field.type = 'number';
            break;

          case 'CmsInputCheckbox':
          case 'InputCheckbox':
            field.type = 'boolean';
            break;

          case 'CmsQuizQuestion':
          case 'QuizQuestion':
            field.type = 'string';
            field.enum = props.options || [];
            break;

          default:
            field.type = 'string';
            break;
        }

        retval.properties[field.name] = field;
      });

    return retval;
  },

  getPageFields(page) {
    if (!page || !page.blocks || !page.blocks.length) {
      return [];
    }

    let retval = [];
    page.blocks
      .filter((b) => !!b['v-model'])
      .forEach((block) => {
        let props = block.props || {};

        let field = {
          name: block['v-model'],
          label: props.label || props.title || block['v-model'],
          type: null,
          options: null,
        };

        switch (block.component) {
          case 'CmsInput':
            switch (props.type) {
              case 'select':
                field.type = 'list';
                field.options = {
                  multiple: props.multiple,
                  values: props.options,
                };
                break;

              case 'text':
              case 'textarea':
                field.type = 'text';
                break;

              case 'number':
                field.type = 'number';
                break;

              case 'checkbox':
                field.type = 'boolean';
                break;
            }
            break;

          case 'CmsInputSelect':
          case 'InputSelect':
            field.type = 'list';
            field.options = {
              multiple: props.multiple,
              values: props.options,
            };
            break;

          case 'CmsInputText':
          case 'CmsInputTextarea':
          case 'InputText':
          case 'InputTextarea':
            field.type = 'text';
            break;

          case 'CmsInputNumber':
          case 'InputNumber':
            field.type = 'number';
            break;

          case 'CmsInputCheckbox':
          case 'InputCheckbox':
            field.type = 'boolean';
            break;

          case 'CmsQuizQuestion':
          case 'QuizQuestion':
            field.type = 'list';
            field.options = {
              multiple: false,
              values: props.options,
            };
            break;

          default:
            field.type = 'text';
            break;
        }

        retval.push(field);
      });

    return retval;
  }
}