<template>
  <div class="emoji-picker">

    <div
      class="emoji-categories"
      v-if="definition.loaded"
    >
      <div
        class="emoji-category"
        v-for="(value, key) in availableGroups"
        :key="key"
        v-bind:class="{selected: selectedGroup == key, 'search-category': key == 0}"
        @click="changeGroup(key)"
      >
        <component
          v-bind:is="providers.group"
          :group="key"
        ></component>
      </div>
    </div>

    <div
      class="list-container"
      v-if="definition.loaded"
    >
      <div
        class="emoji-list"
        v-for="(value, group_id) in availableGroups"
        :key="group_id"
        :id="'emoji-list-'+group_id"
        :data-group-list="group_id"
      >
        <div
          class="emoji-item"
          v-for="emoji in emojisInGroup(group_id)"
          :key="emoji.hexcode"
          @click="selectEmoji(emoji)"
        >
          <component
            v-bind:is="providers[provider]"
            :emoji="emoji"
            @imageLoaded="emoji.image = $event"
          ></component>
        </div>
      </div>
    </div>

    <div class="emoji-filters">
      <div class="search-field">
        <span
          class="icon"
          v-html="searchIcon"
        ></span>
        <input
          type="text"
          class="emoji-search"
          @input="debouncedSearch($event)"
          @focus="skinPickerOpen = false"
        >
        <span
          class="icon close"
          v-if="searchQuery.trim().length > 0"
          v-html="closeIcon"
          @click="searchQuery = ''"
        ></span>
      </div>

      <div
        class="skin-picker"
        v-if="skinsSample"
      >
        <transition name="slide-fade">
          <div
            class="skin-samples"
            v-if="skinPickerOpen"
          >
            <div
              class="skin-sample"
              v-bind:class="{selected: skin == 0}"
              @click="selectSkin(0)"
            >
              <component
                v-bind:is="providers[provider]"
                :emoji="skinsSample"
              ></component>
            </div>

            <div
              class="skin-sample"
              v-for="(emoji, index) in skinsSample.skins"
              :key="emoji.hexcode"
              v-bind:class="{selected: skin == index+1}"
              @click="selectSkin(index+1)"
            >
              <component
                v-bind:is="providers[provider]"
                :emoji="emoji"
              ></component>
            </div>
          </div>
        </transition>
        <transition name="fade">
          <div
            class="selected-skin"
            v-if="!skinPickerOpen"
          >
            <div
              class="skin-sample"
              @click="skinPickerOpen = true; searchQuery = '';"
            >
              <component
                v-bind:is="providers[provider]"
                :emoji="selectedSkin"
                loading="eager"
                :key="selectedSkin.hexcode"
              ></component>
            </div>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import { fetchFromCDN } from 'emojibase';
/* providers*/
import OpenMojiItem from './Provider/OpenMoji.vue';
import UnicodeItem from './Provider/Unicode.vue';
import TwemojiItem from './Provider/Twemoji.vue';
import GlobalGroup from './Provider/Group.vue';

export default {
  name: 'UiEmojiPicker',

  props: {
    version: {
      type: Number,
      required: false,
      default: 12.1,
    },

    language: {
      type: String,
      required: false,
      default: 'en',
      validator: (value) =>
        ['da', 'de', 'en', 'es', 'fr', 'it', 'ja', 'ko', 'ru', 'zh'].indexOf(
          value
        ) >= 0,
    },

    provider: {
      type: String,
      required: false,
      default: 'twemoji',
      validator: (value) =>
        ['openmoji', 'unicode', 'twemoji'].indexOf(value) >= 0,
    },
  },

  data() {
    return {
      providers: {
        openmoji: OpenMojiItem,
        unicode: UnicodeItem,
        twemoji: TwemojiItem,
        group: GlobalGroup,
      },
      definition: {
        loaded: false,
        meta: null,
        emojis: null,
      },
      selectedGroup: null,
      ignoredGroups: ['component'],
      resizeObserver: null,
      skinsSample: null,
      skinPickerOpen: false,
      skin: 0, //0 means default
      filteredEmojisList: [],
      searchQuery: '',
      searchTimer: null,
      searchIcon:
        '<svg enable-background="new 0 0 30.239 30.239" version="1.1" viewBox="0 0 30.239 30.239" xml:space="preserve" xmlns="http://www.w3.org/2000/svg"><path d="m20.194 3.46c-4.613-4.613-12.121-4.613-16.734 0-4.612 4.614-4.612 12.121 0 16.735 4.108 4.107 10.506 4.547 15.116 1.34 0.097 0.459 0.319 0.897 0.676 1.254l6.718 6.718c0.979 0.977 2.561 0.977 3.535 0 0.978-0.978 0.978-2.56 0-3.535l-6.718-6.72c-0.355-0.354-0.794-0.577-1.253-0.674 3.209-4.611 2.769-11.008-1.34-15.118zm-2.121 14.614c-3.444 3.444-9.049 3.444-12.492 0-3.442-3.444-3.442-9.048 0-12.492 3.443-3.443 9.048-3.443 12.492 0 3.444 3.444 3.444 9.048 0 12.492z"/>',
      closeIcon:
        '<svg viewBox="0 0 365.696 365.696" xmlns="http://www.w3.org/2000/svg"><path d="M243.188 182.86L356.32 69.726c12.5-12.5 12.5-32.766 0-45.247L341.238 9.398c-12.504-12.503-32.77-12.503-45.25 0L182.86 122.528 69.727 9.374c-12.5-12.5-32.766-12.5-45.247 0L9.375 24.457c-12.5 12.504-12.5 32.77 0 45.25l113.152 113.152L9.398 295.99c-12.503 12.503-12.503 32.769 0 45.25L24.48 356.32c12.5 12.5 32.766 12.5 45.247 0l113.132-113.132L295.99 356.32c12.503 12.5 32.769 12.5 45.25 0l15.081-15.082c12.5-12.504 12.5-32.77 0-45.25zm0 0"/></svg>',
      historicEmojis: [],
      intersectionObserver: null,
    };
  },

  watch: {
    version: function () {
      this.loadData();
    },

    language: function () {
      this.loadData();
    },

    searchQuery: function (newval) {
      if (newval == '') {
        this.$el.querySelector('input.emoji-search').value = '';

        if (this.historicEmojis.length > 0) {
          this.changeGroup(1);
        } else {
          this.changeGroup(2);
        }
      }
    },
  },

  computed: {
    availableGroups: function () {
      let filteredGroups = {
        0: 'search',
        1: 'history',
      };

      for (var key in this.definition.meta.groups) {
        if (!this.ignoredGroups.includes(this.definition.meta.groups[key])) {
          filteredGroups[2 + parseInt(key)] = this.definition.meta.groups[key];
        }
      }

      return filteredGroups;
    },

    selectedSkin: function () {
      if (this.skin == 0) {
        return this.skinsSample;
      } else {
        return this.skinsSample.skins[this.skin - 1];
      }
    },
  },

  mounted() {
    this.initialize();
  },

  methods: {
    initialize() {
      if (this.isEmojiSupported()) {
        this.loadData();
      } else {
        console.log('emojis are not supported by this system');
      }

      let storedSkin = localStorage.getItem('emoji-picker-skin');
      if (storedSkin !== null) {
        this.skin = storedSkin;
      }

      let storedHistory = localStorage.getItem('emoji-picker-history');
      if (storedHistory !== null) {
        this.historicEmojis = JSON.parse(storedHistory);
      }
    },

    afterLoaded() {
      if (this.historicEmojis.length > 0) {
        this.changeGroup(1);
      } else {
        this.changeGroup(2);
      }

      let options = {
        root: this.$el.querySelector('div.list-container'),
        rootMargin: '0px',
        threshold: 0.8,
      };

      this.intersectionObserver = new IntersectionObserver((entries) => {
        entries.forEach((entry) => {
          let listId = entry.target.dataset.groupList;

          if (entry.isIntersecting) {
            if (listId == 0) {
              if (this.searchQuery.trim().length == 0) {
                if (this.historicEmojis.length > 0) {
                  listId = 1;
                } else {
                  listId = 2;
                }
              }
            }

            if (listId == 1) {
              if (this.historicEmojis.length <= 0) {
                listId = 2;
              }
            }

            this.changeGroup(listId);
          }
        });
      }, options);

      this.$el
        .querySelectorAll('.emoji-list[data-group-list]')
        .forEach((list) => {
          this.intersectionObserver.observe(list);
        });
    },

    debouncedSearch(event) {
      if (this.searchTimer) {
        clearTimeout(this.searchTimer);
      }

      this.searchTimer = setTimeout(() => {
        this.searchBytag(event);
      }, 1000);
    },

    searchBytag(event) {
      this.searchQuery = event.target.value;
      this.changeGroup(0);
    },

    changeGroup(newTab) {
      this.selectedGroup = newTab;
      let list = this.$el.querySelector('#emoji-list-' + newTab);
      list.scrollIntoView(false);
    },

    selectEmoji(emojiDef) {
      if (
        this.historicEmojis.findIndex(
          (emoji) => emoji.hexcode == emojiDef.hexcode
        ) < 0
      ) {
        this.historicEmojis.push(emojiDef);
        localStorage.setItem(
          'emoji-picker-history',
          JSON.stringify(this.historicEmojis)
        );
      }

      emojiDef.provider = this.provider;

      if (this.searchQuery.trim().length > 0) {
        this.searchQuery = '';
        this.changeGroup(1);
      }

      this.$emit('change', emojiDef);
    },

    selectSkin(index) {
      this.skin = index;
      this.skinPickerOpen = false;

      localStorage.setItem('emoji-picker-skin', this.skin);
    },

    async loadData() {
      this.definition.meta = await fetchFromCDN('meta/groups.json').then(
        (meta) => meta
      );

      let fullEmojiDef = await fetchFromCDN(this.language + '/data.json').then(
        (emojis) => emojis
      );

      this.definition.emojis = fullEmojiDef.filter(
        (emoji) => emoji.version <= this.version
      );

      this.getSkinsSample();

      this.definition.loaded = true;

      await this.$nextTick();
      this.afterLoaded();
    },

    getSkinsSample() {
      this.skinsSample = this.definition.emojis.find((def) => {
        return def.name == 'RAISED HAND' && typeof def.skins != 'undefined';
      });
    },

    emojisInGroup(group) {
      let filteredEmojis = [];

      switch (group) {
        case '0':
          if (this.searchQuery.trim().length > 2) {
            let query = this.searchQuery.trim().toLowerCase();

            filteredEmojis = this.definition.emojis.filter((emoji) => {
              let match = false;

              if (Array.isArray(emoji.tags)) {
                emoji.tags.forEach((tag) => {
                  if (
                    query
                      .split(' ')
                      .filter((part) => part.length > 0)
                      .map((part) => part.toLowerCase())
                      .includes(tag)
                  ) {
                    match = true;
                  }
                });
              }

              return match;
            });
          }
          break;

        case '1':
          filteredEmojis = this.historicEmojis;
          break;

        default:
          if (this.definition.emojis) {
            //by group
            filteredEmojis = this.definition.emojis.filter(
              (emoji) => emoji.group == group - 2
            );

            //by skin
            if (this.skin > 0) {
              let result = [];
              filteredEmojis.forEach((emojiDef) => {
                if (typeof emojiDef.skins != 'undefined') {
                  result.push(
                    emojiDef.skins.find((skinDef) => skinDef.tone == this.skin)
                  );
                } else {
                  result.push(emojiDef);
                }
              });

              filteredEmojis = result;
            }

            //end filters
          }
      }

      return filteredEmojis;
    },

    isEmojiSupported() {
      let context, smile;

      if (!document.createElement('canvas').getContext) {
        return false;
      }
      context = document.createElement('canvas').getContext('2d');
      if (typeof context.fillText != 'function') {
        return false;
      }

      smile = String.fromCharCode(55357) + String.fromCharCode(56835);

      context.textBaseline = 'top';
      context.font = '32px Arial';
      context.fillText(smile, 0, 0);
      return context.getImageData(16, 16, 1, 1).data[0] !== 0;
    },
  },
};
</script>

<style scoped>
.emoji-picker {
  --emoji-group-width: max(20px, 1.8rem);
  --emoji-group-background-color: #ffffff;
  --emoji-group-border-width: 1px;
  --emoji-group-border-color: #d7dbdd;
  --emoji-group-border-selected-color: var(--phi-color-main, #4ebbe4);
  --emoji-list-item-width: max(30px, 2rem);
  --emoji-list-translate-x: 0;
}

@media (pointer: fine) {
  .emoji-category {
    min-width: var(--emoji-group-width);
  }

  .emoji-list {
    scrollbar-width: thin;
  }

  .emoji-list::-webkit-scrollbar {
    width: 10px;
  }

  .emoji-list::-webkit-scrollbar-track {
    box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.3);
    border-top: solid 3px #fff;
  }

  .emoji-list::-webkit-scrollbar-thumb {
    background-color: darkgrey;
  }

  .list-container::-webkit-scrollbar {
    display: none;
  }

  .list-container {
    scrollbar-width: none !important;
  }
}

@media (pointer: coarse) {
  .emoji-categories {
    -ms-overflow-style: none;
    scrollbar-width: none;
    scroll-snap-type: x mandatory;
  }

  .emoji-categories::-webkit-scrollbar {
    display: none;
  }

  .emoji-list::-webkit-scrollbar {
    display: none;
  }

  .emoji-list {
    scrollbar-width: none !important;
  }
}

.emoji-picker {
  display: grid;
  grid-template-rows: auto 1fr auto;

  max-width: 100%;
  min-width: 100%;
  max-height: 100%;
  min-height: 100%;

  padding: 0px;
  margin: 0px;
}

.emoji-categories {
  display: flex;
  border-bottom-width: var(--emoji-group-border-width);
  border-bottom-color: var(--emoji-group-border-color);
  border-bottom-style: solid;
  overflow: auto hidden;

  background-color: var(--emoji-group-background-color);
}

.emoji-categories::-webkit-scrollbar {
  height: 10px;
}

.emoji-categories::-webkit-scrollbar-track {
  box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.3);
}

.emoji-categories::-webkit-scrollbar-thumb {
  background-color: darkgrey;
}

.emoji-category {
  width: var(--emoji-group-width);
  min-height: var(--emoji-group-width);
  text-align: center;

  cursor: pointer;
  user-select: none;
  padding: 5px 5px 2px 5px;

  border-bottom: 3px solid transparent;
}

.emoji-category.selected {
  border-bottom-color: var(--emoji-group-border-selected-color);
}

.emoji-category:not(.selected):hover {
  border-bottom-color: var(--emoji-group-border-color);
}

.emoji-category.search-category {
  visibility: hidden;
  display: none;
}

.list-container {
  scroll-snap-type: x mandatory;
  overflow-x: scroll;
  display: flex;
}

.emoji-list {
  scroll-snap-align: start;
  scroll-snap-stop: always;

  min-width: 100%;
  align-items: center;
  justify-content: center;

  display: flex;
  gap: 4px 12px;

  flex-flow: row wrap;
  justify-content: flex-start;
  align-content: flex-start;
  margin: 5px 0;

  overflow: hidden auto;

  transform: translateX(var(--emoji-list-translate-x));
}

.emoji-item {
  width: var(--emoji-list-item-width);
  min-width: var(--emoji-list-item-width);
  text-align: center;

  cursor: pointer;
  user-select: none;
  border-radius: 3px;
}

.emoji-item:hover {
  background-color: rgba(0, 0, 0, 0.2);
}

.emoji-item:empty {
  visibility: hidden;
  display: none;
}

.emoji-filters {
  display: flex;
  gap: 0 10px;
  padding: 4px;
  border-top-width: var(--emoji-group-border-width);
  border-top-color: var(--emoji-group-border-color);
  border-top-style: solid;
  background-color: var(--emoji-group-background-color);
}

.emoji-filters .search-field {
  display: flex;

  border-width: var(--emoji-group-border-width);
  border-color: var(--emoji-group-border-color);
  border-style: solid;
  border-radius: 3px;

  padding: 3px;

  width: 100%;
}

.emoji-filters input {
  border: 0;
  width: 100%;

  font-size: 1em;
  filter: opacity(70%);
}

.emoji-filters input:focus {
  outline: none;
}

.emoji-filters .icon {
  margin-right: 5px;
  margin-top: 2px;
}

.emoji-filters .icon.close {
  cursor: pointer;
}

.emoji-filters .icon.close svg {
  max-height: 0.8em;
  margin-top: 8px;
}

.emoji-filters .icon svg {
  width: 1em;
  filter: opacity(70%);

  margin: 6px 0 0 4px;
}

.emoji-filters .skin-picker {
  display: flex;
  margin-right: 6px;
}

.emoji-filters .skin-picker .skin-samples {
  display: flex;
  padding: 3px;
}

.skin-samples .skin-sample {
  width: var(--emoji-list-item-width);
  min-width: var(--emoji-list-item-width);
  text-align: center;

  cursor: pointer;
  user-select: none;

  border-bottom: 3px solid transparent;
}

.skin-samples .skin-sample:hover {
  border-radius: 3px;
  background-color: rgba(0, 0, 0, 0.2);
}

.skin-samples .skin-sample.selected {
  border-bottom-color: var(--emoji-group-border-selected-color);
}

.emoji-filters .selected-skin {
  padding: 3px;
  margin-right: 6px;
}

.emoji-filters .selected-skin:hover {
  border-radius: 3px;
  background-color: rgba(0, 0, 0, 0.2);
}

.selected-skin .skin-sample {
  width: var(--emoji-list-item-width);
  min-width: var(--emoji-list-item-width);
  text-align: center;

  cursor: pointer;
  user-select: none;
}

.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s ease;
}
.slide-fade-enter,
.slide-fade-leave-to {
  transform: translateX(10px);
  opacity: 0;
}

.fade-enter-active {
  transition: opacity 0.6s;
}
.fade-leave-active {
  transition: none;
}
.fade-enter,
.slide-fade-leave-to {
  opacity: 0;
}
</style>
