<template>
  <div class="block-editor-actions">
    <UiTabs
      container-class="tab-contents"
      v-model="currentTab"
    >
      <UiTab
        v-for="(action, i) in actions"
        :key="i"
        :text="action.title"
        :value="'actiontab_'+i"
        :icon="action.icon || (definition ? definition.icon : '')"
      />
      <template
        v-for="(action, i) in actions"
        v-slot:[`content-actiontab_${i}`]
      >
        <BlockEditorComponent
          :key="i"
          :definition="action"
          v-model="block"
          @input="emitInput"
          :page="page"
        />
      </template>

      <!-- Visibility (v-if) -->
      <UiTab
        text="Visibilidad"
        value="visibility"
        :icon="block['v-if'] ? 'mdi:eye' : 'mdi:eye-outline'"
      />
      <template #content-visibility>
        <VmExpression
          v-model="blockIf"
          :schema="CmsPageRoot.schema"
        />
      </template>

      <!-- validation -->
      <!-- <UiTab
        text="Validacion"
        value="validation"
        :icon="block['validation'] ? 'mdi:check-underline-circle' : 'mdi:check-circle'"
      />
      <template #content-validation>
        <ValidationEditor
          v-model="block['validation']"
          @input="emitInput"
        />
      </template> -->

      <!-- Listeners (v-on) -->
      <UiTab
        text="Eventos"
        value="events"
        icon="mdi:source-fork"
      />
      <template #content-events>
        <BlockListenersEditor
          v-model="block['v-on']"
          @input="emitInput"
        />
      </template>

      <!-- Repeat (v-for) -->
      <UiTab
        text="Datos"
        value="repeat"
        icon="mdi:variable"
      />
      <template #content-repeat>
        <UiField
          label="Variable"
          message="Nombre de variable"
        >
          <input
            type="text"
            class="ui-native"
            v-model="block['v-model']"
            @input="emitInput"
          />
        </UiField>

        <UiField
          label="Repetir"
          message="Crear una iteracion de este bloque por cada elemento de la variable"
        >
          <input
            type="text"
            class="ui-native"
            v-model="block['v-for']"
            @input="emitInput"
          />
        </UiField>
      </template>

      <!-- full editor -->
      <UiTab
        icon="mdi:code-json"
        text="Editor avanzado"
        value="advanced"
        v-if="showAdvancedEditorAction"
      />
      <template #content-advanced>
        <UiInputJson
          v-model="block"
          @input="emitInput"
        />
      </template>
    </UiTabs>
  </div>
</template>

<script>
/*eslint-disable*/
import useCms from '../../mixins/useCms';

import {
  UiConditionEditor,
  UiField,
  UiInputJson,
  UiTab,
  UiTabs,
} from '/packages/ui/components';

import { VmExpression } from '/packages/vm/components';

import BlockListenersEditor from './BlockListenersEditor.vue';
import BlockEditorComponent from './BlockEditorComponent.vue';
import ValidationEditor from '../Validation/ValidationEditor.vue';

export default {
  name: 'BlockEditorActions',
  inject: ['CmsPageRoot'],
  mixins: [useCms],

  components: {
    UiConditionEditor,
    UiField,
    UiInputJson,
    UiTab,
    UiTabs,
    VmExpression,

    BlockListenersEditor,
    BlockEditorComponent,
    ValidationEditor,
  },

  props: {
    value: {
      type: Object,
      required: true,
    },

    page: {
      type: Object,
      required: false,
      default: () => ({}),
    },

    currentAction: {
      type: String,
      required: false,
      default: null,
    },
  },

  data() {
    return {
      block: null,
      definition: null,
      currentTab: null,
    };
  },

  watch: {
    currentAction: {
      immediate: true,
      handler(newValue) {
        this.currentTab = newValue
          ? newValue
          : this.actions.length
          ? 'actiontab_0'
          : 'visibility';
      },
    },

    value: {
      immediate: true,
      async handler(newValue) {
        this.block = JSON.parse(JSON.stringify(newValue));
        this.definition = await this.$cms.getDefinition(this.block);
      },
    },
  },

  computed: {
    blockIf: {
      get() {
        return this.block && this.block['v-if']
          ? this.block['v-if']
          : { or: [] };
      },

      set(value) {
        if (
          !value ||
          (Array.isArray(value.or) && !value.or.length) ||
          (Array.isArray(value.and) && !value.and.length)
        ) {
          this.block['v-if'] = null;
        } else {
          this.block['v-if'] = value;
        }

        this.emitInput();
      },
    },

    actions() {
      if (
        !this.definition ||
        !this.definition.editor ||
        !this.definition.editor.actions
      ) {
        return [];
      }

      return this.definition.editor.actions;
    },

    // Mostrar el editor avanzado cuando la URL contiene ?debug
    showAdvancedEditorAction() {
      return true;
      // return window.location.href.includes('debug');
    },
  },

  methods: {
    emitInput() {
      // !!! Con el "editor avanzado" es posible romper el bloque.
      // Prevenir emitir bloques nulos:
      if (!this.block || !this.block.component) {
        return;
      }

      this.$emit('input', JSON.parse(JSON.stringify(this.block)));
    },
  },
};
</script>

<style lang="scss">
.block-editor-actions {
  .trigger-item {
    border-top: 1px solid #eee;
    cursor: pointer;
    &:hover {
      background-color: rgba(0, 0, 0, 0.03);
    }
  }

  .header-item-close {
    opacity: 0.3;
    cursor: pointer;

    &:hover {
      opacity: 0.7;
    }
  }

  .right-icon {
    width: 52px;
    height: 42px;
  }

  .tab-contents {
    padding: var(--ui-padding);
  }
}
</style>