<template>
  <component
    v-if="component"
    :is="component"
    v-bind="$attrs"
    v-on="$listeners"
  >
    <slot></slot>
  </component>
</template>

<script>
const GoogleMapLine = () => import('./providers/Google/MapLine.vue');
const MapboxMapLine = () => import('./providers/Mapbox/MapLine.vue');

export default {
  name: 'UiMapLine',
  inject: ['baseName'],

  components: {
    GoogleMapLine,
    MapboxMapLine,
  },

  computed: {
    component() {
      return this.baseName() + 'Line';
    },
  },
};
</script>