<template>
  <div class="ui-audio-player">
    <audio
      v-if="url"
      :src="url"
      :type="type"
      controls
      v-bind="$attrs"
    ></audio>
  </div>
</template>

<script>
export default {
  name: 'UiAudio',

  props: {
    url: {
      type: String,
      required: false,
      default: null
    },

    type: {
      type: String,
      required: false,
      default: 'audio/webm'
    }
  }
};
</script>