<template>
  <div class="openmoji-item" v-if="!errorImage">
    <img :src="currentImage" @load="imageLoaded" @error="errorImage = true" :loading="loading">
  </div>
</template>

<script>
export default {
  name: "OpenMojiItem",

  data() {
    return {
      openMojiCDN: "https://cdn.jsdelivr.net/gh/hfg-gmuend/openmoji",
      formatData:{
        svg: {
          path: "/color/svg/",
          extension: "svg"
        },
        png: {
          path: "/color/72x72/",
          extension: "png"
        }
      },
      errorImage: false
    };
  },

  props: {
    emoji: {
      type: Object,
      required: true
    },

    format: {
      type: String,
      required: false,
      default: "png",
      validator: (value) =>
        ["svg", "png"].indexOf(value) >= 0,
    },

    loading: {
      type: String,
      required: false,
      default: "lazy",
      validator: (value) =>
        ["lazy", "eager", "auto"].indexOf(value) >= 0,
    }
  },

  computed: {
    currentImage: function(){
      return this.openMojiCDN +this.formatData[this.format].path+ this.emoji.hexcode.toUpperCase() +"."+this.formatData[this.format].extension;
    }
  },

  methods: {
    imageLoaded(){
      this.$emit("imageLoaded", this.currentImage);
    }
  },

  mounted() {

  },
};
</script>

<style>
.openmoji-item{
  padding: 0 !important;
}

.openmoji-item img{
  max-width: 100%;
}
</style>
