<template>
  <VmExpressionInternal
    :value="value"
    @input="$emit('input', $event)"
  />
</template>

<script>
import VmExpressionInternal from './VmExpressionInternal.vue';

import defFunctions from './definitions/functions/index.js';
import opDefinitions from './definitions/operators/index.js';

export default {
  name: 'VmExpression',
  components: { VmExpressionInternal },

  provide() {
    return {
      VmExpressionRoot: {
        functions: this.allFunctions,
        operators: opDefinitions,
        // schema: this.computedSchema,
        schema: this.schema,
      },
    };
  },

  props: {
    value: {
      required: false,
      default: null,
    },

    plugins: {
      required: false,
      default: null,
    },

    schema: {
      required: false,
      default: null,
    },
  },

  data() {
    return {};
  },

  computed: {
    allFunctions() {
      let retval = defFunctions;

      if (Array.isArray(this.plugins)) {
        this.plugins
          .filter((p) => p && p.functions)
          .forEach((plugin) => {
            retval = { ...retval, ...plugin.functions };
          });
      }

      return retval;
    },

    // computedSchema() {
    //   if (this.plugins && this.plugins[0] && this.plugins[0].schema) {
    //     return this.plugins[0].schema;
    //   }

    //   return this.schema;
    // },
  },
};
</script>