import { getProperty, setProperty, parse } from './utils';

import vmFunctions from './functions/index.js';
import vmOperators from './operators/index.js';

export default class JsonVm {
  constructor() {
    this.functions = Object.assign({}, vmFunctions);
    this.operators = Object.assign({}, vmOperators);
    this.model = null;

    this.onModelSet = null;
  }

  /**
   * Operators
   * some.model.property >= 3
   *
   * {
   *   "op": "number.gte",
   *   "arg1": "{{some.model.property}}",
   *   "arg2": 3
   * }
   */
  async stmtOperator(opName, arg1 = null, arg2 = null) {
    if (typeof this.operators[opName] == 'undefined') {
      throw `undefined operator '${opName}'`;
    }

    switch (opName) {
      case 'and':
        return (await this.eval(arg1)) && (await this.eval(arg2));

      case 'or':
        return (await this.eval(arg1)) || (await this.eval(arg2));
    }

    let sideA = await this.eval(arg1);
    let sideB = await this.eval(arg2);

    return await this.operators[opName].bind(this)(sideA, sideB);
  }

  /**
   * do-then statemant
   * {
   *  "do": {... expr ...},
   *  "assign": "foo.stuff.result", // nombre de variable en donde almacenar el resultado (si se quiere)
   *  "then": {... expr ...},
   * }
   */
  async stmtDo(doStmt, targetVar = null, thenStmt = null) {
    let res = await this.eval(doStmt);
    if (targetVar) {
      setProperty(this.model, targetVar, res);
    }

    return thenStmt ? await this.eval(thenStmt) : res;
  }

  /**
   * Function calls
   * fetch({url: '...'}).then(...)
   *
   * {
   *   "call": "fetch",
   *   "args": {
   *     "url": "https://........"
   *   },
   *   "then": ...
   * }
   *
   */
  async stmtCall(functionName, functionArgs = null) {
    if (typeof this.functions[functionName] == 'undefined') {
      throw `undefined function '${functionName}'`;
    }

    let args = functionArgs ? await this.eval(functionArgs) : null;
    return await this.functions[functionName].bind(this)(args);
  }

  /**
   * IF statement
   * {
   *   "if": "...expr...",
   *   "then": "...expr...",
   *   "else": "...expr..."
   * }
   */
  async stmtIf(condition, exprThen, exprElse = null) {
    let retval = null;
    let boo = await this.eval(condition);
    if (boo) {
      retval = await this.eval(exprThen);
    } else if (exprElse) {
      retval = await this.eval(exprElse);
    }

    return retval;
  }

  /**
   * Switch statement
   * {
   *    "switch": "...expr...",
   *    "case": [
   *      {
   *        "value": "rojo",
   *        "do": "...expr..."
   *      }
   *    ],
   *    "default": "... expr ..."
   * }
  */
  async stmtSwitch(expr, cases = [], defaultCase = null) {
    let res = await this.eval(expr);

    for (let i = 0; i < cases.length; i++) {
      if (cases[i].value == res) {
        return await this.eval(cases[i].do);
      }
    }

    if (defaultCase) {
      return await this.eval(defaultCase);
    }
  }


  /**
   * Chain statement
   * {
   *   "chain": [
   *     {
   *       "info": {..item...},
   *       "do": {...expr...},
   *       "assign": "name of model variable to store results"
   *     },
   *     {
   *       "info": {..item...},
   *       "do": {...expr...},
   *       "assign": "name of model variable to store results"
   *     }
   *   ]
   * }
   *
   */
  async stmtChain(arrChain = []) {
    if (!arrChain.length) {
      return;
    }

    let res;
    for (let i = 0; i < arrChain.length; i++) {
      let link = arrChain[i];
      if (!link.do) {
        continue;
      }

      res = await this.eval(link.do);
      if (link.assign) {
        setProperty(this.model, link.assign, res);
        if (this.onModelSet) {
          this.onModelSet(link.assign, res);
        }
      }
    }

    return res;
  }

  /**
   * Recibe una expresion de tipo CLOSURE:
   * {
   *    arguments: ['arg1', 'arg2', ...],
   *    function: {
   *      call: 'doStuff'
   *    }
   * }
   * equivale a (arg1, arg2) => { return doStuff() }
   *
   * y la ejecuta con los argumentos dados.
   *
   * runClosure(fn, args)  es equivalente a ....
   *
   * let callable = (argsname) => { fn }
   * return callable(args);
   */
  runClosure(fn, arrArgs = []) {
    if (!Array.isArray(arrArgs)) {
      throw 'invalid arguments in function call';
    }

    if (!fn.function) {
      throw 'invalid function';
    }

    let argValues = {};
    if (Array.isArray(fn.arguments)) {
      for (let i = 0; i < fn.arguments.length; i++) {
        let argName = fn.arguments[i];
        if (typeof arrArgs[i] != 'undefined') {
          argValues[argName] = arrArgs[i];
        }
      }
    }

    return this.eval(fn.function, argValues);
  }

  async eval(expr, incomingDataModel = null) {
    if (!expr || (typeof expr != 'string' && typeof expr != 'object')) {
      return expr;
    }

    if (!this.model) {
      this.model = {};
    }

    if (incomingDataModel) {
      this.model = Object.assign({}, this.model, incomingDataModel);
    }

    if (typeof expr == 'string') {
      return parse(expr, this.model);
    }

    if (Array.isArray(expr)) {
      let retval = [];
      for (let i = 0; i < expr.length; i++) {
        retval[i] = await this.eval(expr[i]);
      }
      return retval;
    }

    // Declaracion de funcion inline.  No se evalúa
    if (expr.function) {
      return expr;
    }

    if (expr.call) {
      return this.stmtCall(expr.call, expr.args);
    }

    if (typeof expr.do != 'undefined') {
      return this.stmtDo(expr.do, expr.assign, expr.then);
    }

    if (Array.isArray(expr.chain)) {
      return this.stmtChain(expr.chain);
    }

    if (expr.op) {
      let arg1 = expr.arg1 || (expr.field ? getProperty(this.model, expr.field) : null);
      let arg2 = expr.arg2 || expr.args;
      return this.stmtOperator(expr.op, arg1, arg2);
    }

    if (expr.if) {
      return this.stmtIf(expr.if, expr.then, expr.else);
    }

    if (expr.switch) {
      return this.stmtSwitch(expr.switch, expr.case, expr.default);
    }

    if (expr.not) {
      return !(await this.eval(expr.not));
    }

    if (Array.isArray(expr.or)) {
      let statements = expr.or;
      for (let i = 0; i < statements.length; i++) {
        if (await this.eval(statements[i])) {
          return true;
        }
      }
      return false;
    }

    if (Array.isArray(expr.and)) {
      let statements = expr.and;
      for (let i = 0; i < statements.length; i++) {
        if (!(await this.eval(statements[i]))) {
          return false;
        }
      }
      return true;
    }

    /**
     * The expression is a general object:
     *
     * {
     *   "title": "Hi, this is whatever",
     *   "count": "{{people.length}}",
     *   "things": {
     *     "call": "fetch",
     *     "args": {
     *       "url": "https://......"
     *     }
     *   }
     * }
     */
    let retval = {};
    for (let propertyName in expr) {
      if (!Object.prototype.hasOwnProperty.call(expr, propertyName)) {
        continue;
      }
      retval[propertyName] = await this.eval(expr[propertyName]);
    }

    return retval;
  }
}