<template>
  <div class="page-layout-editor">
    <div class="layout-editor-row">
      <div
        v-for="(column, columnIndex) in row.columns"
        :key="columnIndex"
        class="layout-editor-column"
        :class="{'--endangered': endangeredColumn == columnIndex}"
        :style="{flex: column.flex}"
      >
        <UiDataDialog
          :value="column['v-if']"
          @input="setColumnVisibility(columnIndex, $event)"
        >
          <template #trigger>
            <UiIcon
              class="icon-visibility ui-clickable"
              title="Visibilidad"
              :value="column['v-if'] ? 'mdi:eye' : 'mdi:eye-outline'"
            />
          </template>
          <template #contents="{innerModel, setModel}">
            <UiConditionEditor
              :value="innerModel"
              @input="setModel"
            />
          </template>
        </UiDataDialog>

        <UiIcon
          class="icon-delete-column"
          value="mdi:close-circle"
          @click="removeColumn(columnIndex)"
          @mouseover.native="endangeredColumn = columnIndex"
          @mouseout.native="endangeredColumn = null"
          size="32"
        />

        <UiIcon
          class="icon-add-column --before"
          value="mdi:plus-circle"
          size="32"
          @click="addColumn(columnIndex, true)"
        />

        <UiIcon
          v-if="columnIndex == row.columns.length -1"
          class="icon-add-column --after"
          value="mdi:plus-circle"
          size="32"
          @click="addColumn(columnIndex, false)"
        />

        <div
          class="column-drag-handle"
          v-if="columnIndex < row.columns.length-1"
        ></div>
      </div>
    </div>

    <!-- Ocultar botones cuando crear una fila causaria dejar dos filas seguidas con 1 columna -->
    <template v-if="row.columns.length > 1">
      <UiIcon
        v-if="!page.layout[rowIndex-1] || page.layout[rowIndex-1].columns.length > 1"
        class="icon-add-row --before"
        value="mdi:plus-circle"
        size="32"
        @click="addRow(true)"
      />

      <UiIcon
        v-if="rowIndex == page.layout.length-1"
        class="icon-add-row --after"
        value="mdi:plus-circle"
        size="32"
        @click="addRow(false)"
      />
    </template>
  </div>
</template>

<script>
import { ClickDrag, UiIcon, UiDataDialog, UiConditionEditor } from '/packages/ui';

export default {
  name: 'page-layout-editor',
  components: {
    UiIcon,
    UiDataDialog,
    UiConditionEditor,
  },
  props: {
    // Page object
    value: {
      type: Object,
      required: true,
    },

    rowIndex: {
      type: Number,
      required: true,
    },
  },

  data() {
    return {
      page: null,
      row: null,
      draggers: [],
      endangeredColumn: null,
    };
  },

  watch: {
    value: {
      immediate: true,
      deep: true,
      handler(newValue) {
        // this.page = JSON.parse(JSON.stringify(newValue));
        this.page = newValue;
        this.row = this.page.layout[this.rowIndex];
        this.$nextTick(() => this.setupDraggers());
      },
    },

    rowIndex(newValue) {
      this.row = this.page.layout[newValue];
    },
  },

  destroyed() {
    this.clearDraggers();
  },

  methods: {
    setColumnVisibility(columnIndex, value) {
      this.$set(
        this.page.layout[this.rowIndex].columns[columnIndex],
        'v-if',
        value
      );
      this.emitInput();
    },

    addRow(isBefore) {
      let newRow = { columns: [{ flex: 1, blocks: [] }] };
      this.page.layout.splice(
        isBefore ? this.rowIndex : this.rowIndex + 1,
        0,
        newRow
      );

      // desplazar bloques
      let insertedRowIndex = isBefore ? this.rowIndex : this.rowIndex + 1;
      this.page.blocks.forEach((block) => {
        if (block.layout.row >= insertedRowIndex) {
          block.layout.row++;
        }
      });

      this.emitInput();
    },

    removeRow() {
      if (this.page.layout.length == 1) {
        alert('No se puede borrar');
        return;
      }

      this.page.layout.splice(this.rowIndex, 1);

      // desplazar bloques hacia arriba
      this.page.blocks.forEach((block) => {
        if (block.layout.row >= this.rowIndex) {
          block.layout.row = Math.max(block.layout.row - 1, 0); // fury road
        }
      });

      this.emitInput();
    },

    addColumn(columnIndex, isBefore) {
      this.row.columns.push({
        flex: this.row.columns[columnIndex].flex,
        blocks: [],
      });

      // desplazar bloques
      let insertedColumnIndex = isBefore ? columnIndex : columnIndex + 1;
      this.page.blocks.forEach((block) => {
        if (
          block.layout.row == this.rowIndex &&
          block.layout.column >= insertedColumnIndex
        ) {
          block.layout.column++;
        }
      });

      this.emitInput();
    },

    removeColumn(columnIndex) {
      if (this.row.columns.length == 1) {
        return this.removeRow();
      }

      this.row.columns.splice(columnIndex, 1);

      // desplazar bloques hacia la izquierda
      this.page.blocks.forEach((block) => {
        if (
          block.layout.row == this.rowIndex &&
          block.layout.column >= columnIndex
        ) {
          block.layout.column = Math.max(block.layout.column - 1, 0); // fury road
        }
      });

      this.emitInput();
    },

    clearDraggers() {
      this.draggers.forEach((d) => d.destroy());
      this.draggers = [];
    },

    setupDraggers() {
      this.clearDraggers();

      // crear dragger en todas las columnas, excepto la ultima
      let columns = this.$el.querySelectorAll('.layout-editor-column');

      for (let i = 0; i < columns.length - 1; i++) {
        let column = columns[i];
        let start = { cur: null, sibling: null, sum: 0 };

        this.draggers[i] = new ClickDrag({
          element: column,
          handle: '.column-drag-handle',
          on: {
            start: (/*pos, event*/) => {
              start.cur = column.offsetWidth;
              start.sibling = column.nextSibling.offsetWidth;

              start.sum = 0;
              for (let k = 0; k < columns.length; k++) {
                start.sum += columns[k].offsetWidth;
              }

              for (let k = 0; k < columns.length; k++) {
                this.row.columns[k].flex = Math.floor(
                  (columns[k].offsetWidth * 100000) / start.sum
                );
              }
            },

            drag: (pos /*, event*/) => {
              this.row.columns[i].flex = Math.floor(
                ((start.cur + pos.w) * 100000) / start.sum
              );
              this.row.columns[i + 1].flex = Math.floor(
                ((start.sibling - pos.w) * 100000) / start.sum
              );
            },

            end: (/*pos, event*/) => {
              this.emitInput();
            },
          },
        });
      }
    },

    emitInput() {
      // this.$emit('input', JSON.parse(JSON.stringify(this.row)));
      this.$emit('input', this.page);
    },
  },
};
</script>

<style lang="scss">
.page-layout-editor {
  position: relative;

  .icon-add-column,
  .icon-add-row {
    cursor: pointer;
    color: var(--ui-color-primary);
    background-color: #f3f3f3;

    width: 32px;
    height: 32px;
    border-radius: 50%;

    position: absolute;
    z-index: 4;

    & > * {
      opacity: 0.6;
    }

    &:hover > * {
      opacity: 1;
    }
  }

  .icon-add-row {
    left: 50%;
    margin-left: -16px;

    &.--before {
      top: 0;
      margin-top: -18px;
    }

    &.--after {
      top: 100%;
      margin-top: -18px;
    }
  }

  .icon-add-column {
    top: 50%;
    margin-top: -16px;

    &.--before {
      left: -15px;
    }

    &.--after {
      right: -15px;
    }
  }

  .layout-editor-row {
    display: flex;
    height: 100%;
  }

  .layout-editor-column {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    .column-drag-handle {
      position: absolute;
      cursor: ew-resize;
      width: 20px;
      top: 0;
      bottom: 0;
      right: -10px;
      z-index: 2;
    }

    .icon-delete-column {
      cursor: pointer;
      color: #aaa;

      &:hover {
        color: #fff;
      }
    }

    .icon-visibility {
      position: absolute;
      top: 0;
      right: 4px;
      color: #aaa;

      &:hover {
        color: var(--ui-color-success);
      }
    }

    &.--endangered {
      background-color: var(--ui-color-danger);
      opacity: 0.5;
    }
  }
}
</style>