/*
Validacion de condiciones:

let data = {
  nombre: 'Santiago',
  apellido: 'Cortes',
  edad: 38,
  registro: {
    temperatura: 37,
    sintomas: ['cabeza', 'garganta'],
    comentarios: 'Me siento bien'
  }
};

validate(data.nombre, {
  op: 'string.like',
  args: '%tiago%'
})
> true

validate(data.registro.temperatura, {
  op: 'number.lte',
  args: 36
})
> false

validate(data, {
  '&or': [
    {'nombre': {'&like': '%tiago%'}},
    {'registro.temperatura': {'&lte': 36}}
  ]
})

validate(data, {
  'op': 'or',
  'args': [
    {
      field: 'nombre',
      op: 'string.like',
      args: '%tiago%'
    },
    {
      field: 'registro.temperatura',
      op: 'number.lte',
      args: 36
    },
  ]
})
> true

*/
import getProperty from './getProperty.js';
import setProperty from './setProperty.js';
import interpolate from './interpolate.js';

import booleanOps from './operators/boolean'
import stringOps from './operators/string'
import numberOps from './operators/number'
import arrayOps from './operators/array'
import dateOps from './operators/date'
import legacyOps from './operators/legacy'

const operators = {
  ...booleanOps,
  ...stringOps,
  ...numberOps,
  ...arrayOps,
  ...dateOps,
  ...legacyOps,

  async and(value, args) {
    if (!Array.isArray(args)) {
      return false;
    }

    for (let i = 0; i < args.length; i++) {
      if (!await validateInternal(value, args[i])) {
        return false;
      }
    }

    return true;
  },

  async or(value, args) {
    if (!Array.isArray(args)) {
      return false;
    }

    for (let i = 0; i < args.length; i++) {
      if (await validateInternal(value, args[i])) {
        return true;
      }
    }

    return false;
  },

  async not(value, args) {
    return !(await validateInternal(value, args));
  },

  'enum.anyOf'(value, args) {
    if (!Array.isArray(args)) {
      return false;
    }

    return args.includes(value);
  },

};

async function validateInternal(value, condition) {
  if (!condition) {
    return true;
  }

  let operator = condition.op;
  let field = condition.field ? getProperty(value, condition.field) : value;

  if (typeof operators[operator] == "undefined") {
    return false;
  }

  return await operators[operator](field, condition.args);
}



async function validate(value, condition) {
  condition = sanitizeCondition(condition);
  if (!condition) {
    return true;
  }

  return validateInternal(value, condition);
}



function sanitizeCondition(condition) {
  if (!condition || typeof condition != 'object') {
    return null;
  }

  if (typeof condition.field == 'undefined' && typeof condition.op == 'undefined' && typeof condition.args == 'undefined') {
    return parseCondition(condition);
  }

  return condition;
}


/*
Convierte una condicion de la forma:
{"&or": [
  {"firstName": {"&eq": "hola"}},
  {"lastName": {"&eq": "mundo"}}
]}

a la forma

{
  "field": null,
  "op": "or",
  "args": [
    {
      "field": "firstName",
      "op": "eq",
      "args": "hola"
    },
    {
      "field": "lastName",
      "op": "eq",
      "args": "mundo"
    }
  ]
}
*/
function parseCondition(condition, field = null) {
  if (!condition || typeof condition != 'object') {
    console.warn('invalid condition', condition);
    return null;
  }

  let firstKey = Object.keys(condition)[0];
  let args = condition[firstKey];

  return firstKey[0] == '&'
    ? {
      field,
      op: firstKey.substring(1),
      args: !field ? args.map((c) => parseCondition(c)) : args, // si no hay field, se entiende que al condicion es &and o &or
    }
    : parseCondition(args, firstKey);
}


/*
Convierte un objeto
{
  field: 'firstName',
  op: 'equals',
  args: 'mundo'
}
a
{firstName: {'&equals': 'mundo'}}
*/
function renderCondition(condition) {
  return condition.field
    ? { [condition.field]: { ['&' + condition.op]: condition.args } }
    : { ['&' + condition.op]: condition.args.map(renderCondition) };
}



export { validate, sanitizeCondition, parseCondition, renderCondition, getProperty, setProperty, interpolate }