import Vue from 'vue';
import { UiToast } from '../../components/UiToast';

export default {
  computed: {
    $toast() {
      return {
        open: (propsData) => {
          if (typeof propsData == 'string') {
            propsData = {
              text: propsData
            };
          }

          return new (Vue.extend(UiToast))({
            el: document.createElement('div'),
            propsData
          })
        }
      };
    }
  }
}