<template>
  <div class="PagePanel">
    <UiTabs value="setup">
      <UiTab
        value="setup"
        icon="mdi:motion-play-outline"
        text="Inicialización"
      />
      <template #content-setup>
        <VmExpression
          v-model="pageSetup"
          :schema="CmsPageRoot.schema"
        />
      </template>

      <UiTab
        value="watchers"
        icon="mdi:playlist-check"
        text="Watchers"
      />
      <template #content-watchers>
        <PageWatchEditor v-model="pageWatch" />
      </template>

      <UiTab
        value="raw"
        icon="mdi:code-json"
        text="Editor avanzado"
      />
      <template #content-raw>
        <UiInputJson
          :value="value"
          @input="$emit('input', $event)"
          style="min-height: 450px"
        />
      </template>
    </UiTabs>
  </div>
</template>

<script>
import { VmExpression } from '/packages/vm/components';
import PageWatchEditor from './PageWatchEditor.vue';
import { UiInputJson, UiTabs, UiTab } from '/packages/ui';

export default {
  name: 'PagePanel',
  inject: ['CmsPageRoot'],
  components: { VmExpression, PageWatchEditor, UiInputJson, UiTabs, UiTab },

  props: {
    value: {
      required: false,
      default: null,
    },
  },

  computed: {
    pageWatch: {
      get() {
        return this.value && this.value.watch ? this.value.watch : null;
      },

      set(value) {
        let watch = Array.isArray(value) && value.length ? value : null;
        this.$emit('input', Object.assign({}, this.value, { watch }));
      },
    },

    pageSetup: {
      get() {
        return this.value && this.value.setup
          ? this.value.setup
          : { chain: [] };
      },

      set(value) {
        let setup = null;

        if (!value || (Array.isArray(value.chain) && !value.chain.length)) {
          setup = null;
        } else {
          setup = value;
        }

        this.$emit('input', Object.assign({}, this.value, { setup }));
      },
    },
  },
};
</script>