<template>
  <div class="ui-input-search ui-inset ui-breathe">
    <UiIcon
      class="ui-input-search-icon"
      size="22"
      value="mdi:magnify"
    />

    <input
      type="text"
      :placeholder="placeholder"
      v-model="innerValue"
      @input="debounceInput"
      @keyup.enter="hardInput"
    />
  </div>
</template>

<script>
import UiIcon from '../UiIcon/UiIcon.vue';

export default {
  name: 'UiInputSearch',

  components: {
    UiIcon,
  },

  props: {
    /**
     * @model
     */
    value: {
      type: String,
      required: false,
      default: null,
    },

    placeholder: {
      type: String,
      required: false,
      default: null,
    },

    debounce: {
      type: [String, Number],
      required: false,
      default: 400,
    },

    minLength: {
      type: Number,
      required: false,
      default: 3,
    },
  },

  data() {
    return {
      innerValue: '',
      timer: null,
    };
  },

  watch: {
    value: {
      immediate: true,
      handler(newValue) {
        this.innerValue = newValue;
      },
    },
  },

  methods: {
    focus() {
      return this.$nextTick(() => this.$el.querySelector('input').select());
    },

    hardInput() {
      clearTimeout(this.timer);

      let value = this.innerValue.trim();
      if (value.length >= this.minLength) {
        this.$emit('input', value);
      }
    },

    debounceInput() {
      clearTimeout(this.timer);

      if (!this.innerValue.length) {
        this.$emit('input', null);
        return;
      }

      this.timer = setTimeout(() => {
        let trimmed = this.innerValue.trim();
        if (trimmed.length >= this.minLength) {
          this.$emit('input', this.innerValue);
        }
      }, parseInt(this.debounce));
    },
  },
};
</script>

<style lang="scss">
.ui-input-search {
  display: flex;

  .ui-input-search-icon {
    color: inherit;
    margin: 0 8px;
  }

  & > input {
    flex: 1;

    display: block;
    width: 100%;
    outline: none;
    border: none;
    color: inherit;
    background-color: transparent;

    font-size: 1em;
    padding: 8px;

    &::placeholder,
    &::-webkit-input-placeholder,
    &:-ms-input-placeholder {
      color: #9fa4aa;
    }
  }
}
</style>