const UiInput = () => import('../../UiInput/UiInput.vue')

export default {
  'enum.anyOf': {
    text: 'cualquiera de',
    component: UiInput,
    props: (schema) => ({
      type: 'select',
      multiple: true,
      native: false,
      options: schema.enum,
      ...schema.props
    }),

    redact(condition) {
      if (!condition || !Array.isArray(condition.args)) {
        return `cualquiera de`
      }

      let args = condition.args.concat([]);
      let last = args.pop();
      return args.length ? [args.join(', '), last].join(' o ') : last;
    }
  }
}