/* 
Crear un operador llamado "array.length.*" por cada operador de "number"
e.j.  array.length.eq, array.length.gte, array.length.between, etc
que realice la validacion apropiada usando la longitud del arreglo como argumento
*/

import numberOps from './number'

let lengthOps = {};
for (let prop in numberOps) {
  if (Object.prototype.hasOwnProperty.call(numberOps, prop)) {
    let numberFunction = numberOps[prop];
    let opName = prop.replace('number.', 'array.length.');
    lengthOps[opName] = (value, args) => numberFunction(value.length, args);
  }
}

export default {
  ...lengthOps,

  'array.eq'(value, args) {
    if (value.length != args.length) {
      return false;
    }
    let arr1 = value.concat().sort();
    let arr2 = args.concat().sort();
    return arr1.every((value, index) => value == arr2[index]);
  },

  'array.includes'(value, args) {
    return value.includes(args);
  },

  'array.hasAny'(value, args) {
    for (let i = 0; i < args.length; i++) {
      if (value.includes(args[i])) {
        return true;
      }
    }
    return false;
  },

  'array.hasAll'(value, args) {
    for (let i = 0; i < args.length; i++) {
      if (!value.includes(args[i])) {
        return false;
      }
    }
    return true;
  },

  'array.empty'(value) {
    return value.length == 0;
  },

  'array.nempty'(value) {
    return value.length > 0;
  }
}