<template>
  <div class="twemoji-item" v-if="!errorImage">
    <img :src="currentImage" @error="setNotSupported($event)" @load="imageLoaded" :loading="loading">
  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: "TwemojiItem",

  data() {
    return {
      TwemojiCDN: "https://cdn.jsdelivr.net/gh/twitter/twemoji",
      formatData:{
        svg: {
          path: "/assets/svg/",
          extension: "svg"
        },
        png: {
          path: "/assets/72x72/",
          extension: "png"
        }
      },
      errorEmoji: "❔",
      filteredHexcode: null,
      errorTryCount: 0,
      errorImage: false
    };
  },

  props: {
    emoji: {
      type: Object,
      required: true
    },

    format: {
      type: String,
      required: false,
      default: "png",
      validator: (value) =>
        ["svg", "png"].indexOf(value) >= 0,
    },

    loading: {
      type: String,
      required: false,
      default: "lazy",
      validator: (value) =>
        ["lazy", "eager", "auto"].indexOf(value) >= 0,
    }
  },

  computed: {
    currentImage: function(){
      return this.TwemojiCDN + this.formatData[this.format].path + this.filteredHexcode + "."+ this.formatData[this.format].extension;
    }
  },

  methods: {
    imageLoaded(){
      this.$emit("imageLoaded", this.currentImage);
    },

    setNotSupported(event){
      if(this.errorTryCount == 0){
        this.filteredHexcode =  this.filteredHexcode.replace(/^0+/, "");
        this.filteredHexcode =  this.filteredHexcode.replace(/-fe0f/g, "");
        this.errorTryCount = 1;
      }

      if(this.errorTryCount == 1){
        let hexCode = this.errorEmoji.codePointAt(0).toString(16);
        this.filteredHexcode =  this.filteredHexcode.replace(/^0+/, "");
        this.errorTryCount = 2;
      }
    }
  },

  mounted() {
    this.filteredHexcode = this.emoji.hexcode.toLowerCase();
  },
};
</script>

<style>
.twemoji-item{
  padding: 3px !important;
}

.twemoji-item img{
  max-width: 100%;
}
</style>

