<template>
  <component
    v-if="component"
    :is="component"
    v-bind="$attrs"
    v-on="$listeners"
  >
    <slot></slot>
  </component>
</template>

<script>
const GoogleMapMarker = () => import('./providers/Google/MapMarker.vue');
const MapboxMapMarker = () => import('./providers/Mapbox/MapMarker.vue');

export default {
  name: 'UiMapMarker',
  inject: ['baseName'],

  components: {
    GoogleMapMarker,
    MapboxMapMarker,
  },

  computed: {
    component() {
      return this.baseName() + 'Marker';
    },
  },
};
</script>