<template>
  <UiDrawer
    v-model="condition"
    v-bind="$attrs"
  >
    <template #trigger>
      <UiItem
        class="ui-clickable"
        icon="mdi:circle-medium"
        :text="conditionTitle"
        :secondary="conditionDescription"
      />
    </template>
    <template #contents="{ close }">
      <div class="condition-primitive-contents">

        <div class="ui-row --tight --top">
          <select
            class="select-field-op"
            v-if="availableConditions.length"
            v-model="condition.op"
          >
            <optgroup
              v-for="optgroup in availableConditions"
              :key="optgroup.label"
              :label="optgroup.label"
            >
              <option
                v-for="op in optgroup.options"
                :key="op.value"
                :value="op.value"
              >{{ op.text }}</option>
            </optgroup>
          </select>
          <component
            v-if="argsEditorComponent"
            :is="argsEditorComponent.component"
            v-bind="argsEditorComponent.props"
            v-model="condition.args"
          ></component>
        </div>

        <footer class="data-drawer-footer">
          <button
            type="button"
            class="ui-button --main"
            @click="accept(); close()"
          >Aceptar</button>
          <button
            type="button"
            class="ui-button --cancel"
            @click="cancel(); close()"
          >Cancelar</button>
        </footer>
      </div>

    </template>
  </UiDrawer>
</template>

<script>
import { schemaGetProperty } from './functions/schema';
import map from './map';
import { UiDrawer } from '../UiDrawer';
import { UiItem } from '../UiItem';
import { UiInputJson } from '../UiInputJson';

export default {
  name: 'ConditionPrimitive',

  components: { UiDrawer, UiItem, UiInputJson },

  props: {
    value: {
      type: Object,
      required: false,
      default: null,
    },

    schema: {
      type: Object,
      required: false,
      default: null,
    },

    customConditions: {
      type: Object,
      required: false,
      default: null,
    },
  },

  data() {
    return {
      map,
      condition: {
        field: '',
        op: null,
        args: null,
      },

      args: null,
    };
  },

  watch: {
    value: {
      immediate: true,
      handler(newValue) {
        this.condition = JSON.parse(JSON.stringify(newValue));
      },
    },
  },

  computed: {
    fieldSchema() {
      let field = this.condition.field;
      if (!field || !this.schema) {
        return null;
      }

      return schemaGetProperty(this.schema, field);
    },

    availableConditions() {
      let retval = [];
      for (let type in this.map) {
        if (!Object.prototype.hasOwnProperty.call(this.map, type)) {
          continue;
        }

        if (
          this.fieldSchema &&
          this.fieldSchema.type != type &&
          type != 'enume'
        ) {
          continue;
        }

        if (type == 'enume' && (!this.fieldSchema || !this.fieldSchema.enum)) {
          continue;
        }

        let optGroup = {
          label: type,
          options: [],
        };

        for (let conditionName in this.map[type]) {
          if (
            !Object.prototype.hasOwnProperty.call(this.map[type], conditionName)
          ) {
            continue;
          }
          let operator = this.map[type][conditionName];

          optGroup.options.push({
            text: operator.text,
            value: conditionName,
          });
        }

        retval.push(optGroup);
      }

      return retval;
    },

    conditionDefinition() {
      if (!this.condition || !this.condition.op) {
        return null;
      }

      if (
        this.customConditions &&
        typeof this.customConditions[this.condition.op] != 'undefined'
      ) {
        return this.customConditions[this.condition.op];
      }

      for (let type in this.map) {
        if (!Object.prototype.hasOwnProperty.call(this.map, type)) {
          continue;
        }

        for (let op in this.map[type]) {
          if (!Object.prototype.hasOwnProperty.call(this.map[type], op)) {
            continue;
          }

          if (op == this.condition.op) {
            return this.map[type][op];
          }
        }
      }

      return null;
    },

    argsEditorComponent() {
      if (this.conditionDefinition) {
        if (!this.conditionDefinition.component) {
          return null;
        }

        let component = this.conditionDefinition.component;
        let props = this.conditionDefinition.props;
        if (typeof props == 'function') {
          let curSchema = this.fieldSchema || {
            type: null,
            title: null,
            props: null,
            enum: null,
            items: null
          };

          props = props(curSchema);
        }

        return { component, props };
      }

      return {
        component: 'UiInputJson',
        props: { type: 'json' },
      };
    },

    conditionTitle() {
      if (this.fieldSchema && this.fieldSchema.title) {
        return this.fieldSchema.title;
      }

      if (!this.conditionDefinition) {
        return this.condition.op;
      }

      return this.conditionDefinition.text || this.condition.op;
    },

    conditionDescription() {
      if (!this.conditionDefinition) {
        return null;
      }

      if (
        !!this.condition &&
        typeof this.conditionDefinition.redact == 'function'
      ) {
        let customRedaction = this.conditionDefinition.redact(this.condition);
        if (customRedaction) {
          return customRedaction;
        }
      }

      let defaultString = this.conditionDefinition.text + ' ';
      if (
        this.condition.args &&
        typeof this.condition.args.toString == 'function'
      ) {
        defaultString += '"' + this.condition.args.toString() + '"';
      }
      return defaultString;
    },
  },

  methods: {
    accept() {
      this.$emit('input', JSON.parse(JSON.stringify(this.condition)));
    },

    cancel() {
      this.condition = JSON.parse(JSON.stringify(this.value));
      this.$emit('cancel');
    },
  },
};
</script>

<style lang="scss">
.condition-primitive-contents {
  padding-left: 48px;

  .data-drawer-footer {
    margin-top: 24px;
  }

  .select-field-op {
    padding: 0;
    margin: var(--ui-padding);
    background-color: transparent;
    border: none;
    font-family: var(--ui-font-secondary);
    font-weight: bold;
    font-size: 0.9em;
    outline-color: var(--ui-color-primary);

    cursor: pointer;
  }
}
</style>