const MediaLoremIpsum = () => import('./MediaLoremIpsum.vue');
import { UiFormGenerator } from '/packages/ui';

export default {
  // "disabled": true,

  "name": "MediaLoremIpsum",
  "title": "Lorem Ipsum",
  "icon": "g:translate",
  "description": "Lorem ipsum dolor sit amet",

  "block": {
    "component": MediaLoremIpsum,
    "props": {
      "nParagraphs": 2,
      "nWords": 56
    }
  },

  "editor": {
    toolbar: {
      "v-model": "block",
      "component": UiFormGenerator,
      "props": {
        "inline": true,
        "schema": {
          "fields": [
            {
              "type": "text",
              "label": "Cantidad de parrafos",
              "model": "props.nParagraphs",
              "props": {
                "size": 2,
                "style": "width: 80px"
              }
            },
            {
              "type": "text",
              "label": "Cantidad de palabras",
              "model": "props.nWords",
              "props": {
                "size": 3,
                "style": "width: 80px"
              }
            }
          ]
        }
      }
    },

    "actions": [
      {
        "title": "Editor de Lipsum",
        "component": UiFormGenerator,
        "v-model": "block",
        "props": {
          "schema": {
            "fields": [
              {
                "type": "number",
                "label": "Cantidad de parrafos",
                "model": "props.nParagraphs"
              },
              {
                "type": "number",
                "label": "Cantidad de palabras",
                "model": "props.nWords"
              }
            ]
          }
        }
      }
    ]
  }
};