<template>
  <div class="cms-block">
    <CmsBlockComponent
      v-for="(blockComponent, i) in renderedBlocks"
      :key="i"
      :component-class="blockComponent.props.class"
      :name="blockComponent.component"
      v-bind="blockComponent.props"
      v-on="blockComponent.listeners"
      @block:mounted="onBlockMounted"
      @hook:mounted="onHookMounted(blockComponent)"
      @hook:destroyed="onHookDetroyed(blockComponent)"
    ></CmsBlockComponent>
  </div>
</template>

<script>
import { getProperty } from '../../functions';
import { VM } from '/packages/vm';

import useCms from '../../mixins/useCms.js';
import CmsBlockComponent from './CmsBlockComponent.vue';

export default {
  name: 'CmsBlock',
  mixins: [useCms],
  components: { CmsBlockComponent },

  inject: {
    cmsSettings: {
      default: null,
    },
  },

  props: {
    value: {
      type: Object,
      required: true,
    },

    data: {
      type: Object,
      required: false,
      default: () => ({}),
    },

    vm: {
      type: Object,
      required: false,
      default: () => {
        let retval = new VM();
        retval.onModelSet = (varname, value) =>
          this.setVariable(varname, value);

        return retval;
      },
    },

    showHidden: {
      type: Boolean,
      required: false,
      default: false,
    },
  },

  data() {
    return {
      definition: null,
      renderedBlocks: [],
    };
  },

  watch: {
    value: {
      immediate: true,
      deep: true,
      async handler(newValue) {
        if (!newValue) {
          return;
        }

        this.definition = await this.$cms.getDefinition(newValue);
        this.computeVisibility();
      },
    },

    data: {
      deep: true,
      handler() {
        this.computeVisibility();
      },
    },
  },

  computed: {
    eventListeners() {
      let listeners = {};

      if (this.value['v-model']) {
        listeners.input = (newValue) =>
          this.setVariable(this.value['v-model'], newValue);
      }

      if (!this.value['v-on']) {
        return listeners;
      }

      for (let eventName in this.value['v-on']) {
        if (
          !Object.prototype.hasOwnProperty.call(this.value['v-on'], eventName)
        ) {
          continue;
        }
        listeners[eventName] = ($event) =>
          this.onBlockEvent(eventName, $event, this.value['v-on'][eventName]);
      }
      return listeners;
    },
  },

  methods: {
    onBlockMounted(ref) {
      if (this.value.ref) {
        this.$emit('ref:mounted', { name: this.value.ref, component: ref });
      }
    },

    onHookMounted(block) {
      if (block.mounted) {
        this.onBlockEvent('mounted', null, block.mounted);
      }
    },

    onHookDetroyed(block) {
      if (block.destroyed) {
        this.onBlockEvent('destroyed', null, block.destroyed);
      }

      if (this.value.ref) {
        this.$emit('ref:destroyed', { name: this.value.ref });
      }
    },

    async computeVisibility() {
      if (!this.value || !this.definition) {
        return;
      }

      let isVisible = true;
      if (this.value['v-if']) {
        try {
          isVisible = await this.vm.eval(this.value['v-if'], this.data);
        } catch (validationError) {
          isVisible = false;
        }
      }

      if (!isVisible) {
        this.renderedBlocks = [];
        return;
      }

      let iterationArray = [null];
      if (this.value['v-for']) {
        iterationArray = this.getVariable(this.value['v-for']);
        if (!Array.isArray(iterationArray)) {
          console.warn("Invalid 'v-for'", this.value);
          return;
        }
      }

      for (let i = 0; i < iterationArray.length; i++) {
        let vmData = Object.assign({}, this.data, {
          $item: iterationArray[i],
          $index: i,
        });

        let props = this.value.props
          ? await this.vm.eval(this.value.props, vmData)
          : null;

        if (this.value['v-model']) {
          props = Object.assign({}, props, {
            value: this.getVariable(this.value['v-model']),
          });
        }

        let finalBlock = Object.assign({}, this.value, {
          component: this.definition.block.component,
          props,
          listeners: this.eventListeners,
        });

        this.$set(this.renderedBlocks, i, finalBlock);
      }
    },

    async onBlockEvent(eventName, $event, configuredAction) {
      try {
        let dataModel = Object.assign({}, this.data, { $event });
        await this.vm.eval(configuredAction, dataModel);
      } catch (err) {
        console.error('Block.vue@onBlockEvent', eventName, err);
        return null;
      }
    },

    getVariable(propertyName) {
      return getProperty(this.data, propertyName);
    },

    setVariable(propertyName, value) {
      let curvar = this.data;
      let parts = propertyName.split('.');
      let len = parts.length;

      for (let i = 0; i < len - 1; i++) {
        if (typeof curvar[parts[i]] == 'undefined' || !curvar[parts[i]]) {
          this.$set(curvar, parts[i], {});
        }
        curvar = curvar[parts[i]];
      }

      this.$set(curvar, parts[len - 1], value);

      this.$emit('update:data', this.data);
    },
  },
};
</script>

<style lang="scss">
.cms-block {
  & > .--hidden {
    opacity: 0.5;
  }
}
</style>