import Vue from 'vue'

const Cms = new Vue({
  data() {
    return {
      blocks: {},
      actions: {}
    }
  },

  methods: {
    plugin(pluginObject) {
      Object.assign(this.blocks, pluginObject.blocks);
      Object.assign(this.actions, pluginObject.actions);
    },
    // component(componentName, definition) {
    //   if (!definition) {
    //     return;
    //   }
    //   this.$set(this.blocks, componentName, definition);
    // },

    async getDefinition(block) {
      if (!block || !block.component) {
        return null;
      }

      let definitionId = block.type || block.component;
      // let definitionId = block.component;

      if (typeof this.blocks[definitionId] != 'undefined') {
        return this.blocks[definitionId];
      }

      if (definitionId.substr(0, 3) == 'Cms' && typeof this.blocks[definitionId.substr(3)] != 'undefined') {
        // Fallback: Ver si existe un componente SIN el prefijo "Cms"  (i.e.  CmsSomeComponent se busca como SomeComponent)
        return this.blocks[definitionId.substr(3)];
      }

      /*

      // name to file correspondence:
      // CmsMediaHtml  -> Cms/Media/Html.js
      let file = definitionId.split(/(?=[A-Z])/).join('/') + '.js';
      return import(`../blocks/${file}`)
        .then(module => module.default)

      */
    },

    async getActionDefinition(actionName) {
      if (!actionName) {
        return null;
      }

      if (typeof this.actions[actionName] != 'undefined') {
        return this.actions[actionName];
      }

      return null;
    },
  }
});


/* !!! load built-in plugins !!! */
import pluginCode from '../plugins/core'
Cms.plugin(pluginCode);

import pluginMedia from '../plugins/media'
Cms.plugin(pluginMedia);

import pluginInput from '../plugins/input'
Cms.plugin(pluginInput);

import pluginSchool from '../plugins/school'
Cms.plugin(pluginSchool);

import pluginGoogle from '../plugins/google'
Cms.plugin(pluginGoogle);

export default Cms