function schemaGetPrimitiveFields(schema, prefix = null) {
  if (!schema || !schema.type) {
    return [];
  }

  let retval = [];

  switch (schema.type) {
    case 'object':
      for (let propName in schema.properties) {
        if (!Object.prototype.hasOwnProperty.call(schema.properties, propName)) {
          continue;
        }

        let innerFields = schemaGetPrimitiveFields(
          schema.properties[propName],
          prefix ? `${prefix}.${propName}` : propName
        );

        retval = [...retval, ...innerFields];
      }
      return retval;

    case 'string':
    case 'number':
    case 'boolean':
    case 'date':
    case 'array':
      // default:
      retval.push(Object.assign({ src: prefix }, schema));
      break;
  }

  return retval;
}

function schemaGetProperty(schema, propName) {
  if (!propName || !schema) {
    return null;
  }

  let parts = propName.split('.');
  let target = schema;
  for (let i = 0; i < parts.length; i++) {
    let part = parts[i];
    if (
      typeof target.properties == 'undefined' ||
      typeof target.properties[part] == 'undefined'
    ) {
      return null;
    }
    target = target.properties[part];
  }

  return target;
}

function schemaMerge(target, source) {
  if (!source) {
    return target;
  }

  target.type = source.type;
  target.properties = Object.assign({}, target.properties, source.properties);
  target.props = Object.assign({}, target.props, source.props);

  return target;
}

export { schemaGetPrimitiveFields, schemaGetProperty, schemaMerge }