<template>
  <div
    class="cms-editor-layout"
    :class="{'--open': !!currentAction, '--closed': !currentAction}"
  >
    <div class="block-editor-toolbar ui-toolbar">
      <!-- Drag handle -->
      <UiIcon
        value="mdi:cursor-move"
        style="cursor:grab"
        class="ui-clickable toolbar-icon block-drag-handle"
      />

      <!-- Custom toolbar components -->
      <div class="slot-toolbar ui-group">
        <slot name="toolbar"></slot>
      </div>

      <!-- Visibility (v-if) -->
      <UiIcon
        class="ui-clickable toolbar-icon toolbar-icon-visibility"
        :class="{'--enabled': !!value['v-if']}"
        title="Visibilidad"
        :value="value['v-if'] ? 'mdi:eye' : 'mdi:eye-outline'"
        @click="currentAction = 'visibility'"
      />

      <!-- ui model built in (v-model) -->
      <UiInputWedge
        v-if="definition && definition.block && definition.block['v-model']"
        class="toolbar-wedge-model"
        :class="{'--enabled': !!value['v-model']}"
        icon="mdi:variable"
        title="Variable"
        :value="value['v-model']"
        @input="$emit('input', Object.assign({}, value, {'v-model': $event}))"
      />

      <!-- Global block settings -->
      <UiIcon
        class="ui-clickable toolbar-icon toolbar-icon-settings"
        title="Opciones"
        value="g:settings"
        @click="currentAction = 'settings'"
      />

      <!-- Delete -->
      <UiIcon
        class="ui-clickable toolbar-icon toolbar-icon-delete"
        title="Eliminar"
        value="g:delete"
        @click="onClickDelete"
      />
    </div>

    <div class="block-editor-body theme-block">
      <slot name="default"></slot>
    </div>

    <UiPanel
      :open="!!currentAction"
      @close="currentAction = null"
    >
      <div class="block-editor-panel-contents">
        <UiItem
          class="header-item"
          :text="definition ? definition.title : value.component"
          :icon="definition ? definition.icon : ''"
        >
          <template #right>
            <UiIcon
              @click="currentAction = null"
              value="mdi:close"
              class="right-icon header-item-close"
            />
          </template>
        </UiItem>

        <BlockEditorActions
          :value="value"
          @input="$emit('input', $event)"
          :current-action.sync="currentAction"
        />
      </div>
    </UiPanel>

  </div>
</template>

<script>
import { UiPanel, UiItem, UiIcon, UiInputWedge } from '/packages/ui';
import BlockEditorActions from '../CmsBlockEditor/BlockEditorActions.vue';

export default {
  name: 'EditorLayout',

  components: {
    BlockEditorActions,

    UiPanel,
    UiIcon,
    UiItem,
    UiInputWedge,
  },

  props: {
    value: {
      type: Object,
      required: true,
    },

    definition: {
      type: Object,
      required: false,
      default: null,
    },
  },

  data() {
    return {
      currentAction: null,
    };
  },

  methods: {
    onClickDelete() {
      if (!confirm('Eliminar este bloque?')) {
        return;
      }
      this.$emit('delete');
    },
  },
};
</script>

<style lang="scss">
.cms-editor-layout {
  position: relative;

  .block-editor-toolbar {
    z-index: 2;
    position: absolute;
    left: 0;
    bottom: 100%; // top position
    // top: 100%; // bottom position
    // z-index: 999;

    transition: opacity var(--ui-duration-snap);
    opacity: 0;
    pointer-events: none;
    flex-wrap: nowrap;
    width: max-content; // No soportado en IE ni EDGE !!!

    .slot-toolbar {
      margin-right: var(--ui-breathe);
    }

    .toolbar-wedge-model.--enabled .ui-icon,
    .toolbar-icon.--enabled {
      color: #fff;
      color: var(--ui-color-success);
    }
  }

  &:hover, 
  &.--focused {
    .theme-block {
      // border: 1px solid var(--ui-color-primary); // rompe el margin-collapse entre bloques
      // background-color: #ffffdf;
      border-radius: var(--ui-radius);
      box-shadow: 0 0 0px 1px var(--ui-color-primary);
    }

    & > .block-editor-toolbar {
      opacity: 1;
      pointer-events: initial;
    }
  }

  // Resaltar el bloque actual encima del SCRIM del UiPanel
  &.--open .block-editor-body {
    position: relative;
    z-index: 3;
    background-color: #ffffdf;
  }
}
</style>