<template>
  <div
    class="ui-table-cell"
    :class="`type-${type}`"
  >
    <ul v-if="type == 'array'">
      <li
        v-for="(itemValue, i) in value"
        :key="i"
      >
        <TableCell
          :column="column.items"
          :value="itemValue"
        />
      </li>
    </ul>

    <span
      v-else-if="enumValue"
      class="enum-value"
    >{{ enumValue.text }}</span>

    <span v-else-if="type == 'string'">{{ value }}</span>
    <span v-else-if="type == 'number'">{{ value }}</span>
    <span v-else-if="type == 'date'">{{ $ts(value, columnProps.format || 'date') }}</span>

    <span v-else-if="type == 'timestamp'">{{ $ts(value, columnProps.format || null) }}</span>
    <span v-else-if="type == 'day'">{{ $date(value, columnProps.format || 'day') }}</span>
    <span v-else-if="type == 'currency'">{{ $cr(value, columnProps.currency) }}</span>
    <span v-else-if="type == 'boolean'">{{ value && value != "0" ? 'SI' : 'NO' }}</span>

    <div
      v-else-if="type == 'html'"
      v-html="value"
    ></div>

    <img
      v-else-if="type == 'image'"
      :src="value"
      style="max-width:64px; max-height: 64px;"
    />

    <a
      v-else-if="type == 'link'"
      :href="value"
      :target="columnProps.target || '_blank'"
    >{{ columnProps.text || value }}</a>

    <div
      v-else-if="type == 'file'"
      class="file-list"
    >
      <a
        v-for="(file,i) in value"
        :key="i"
        :href="file.url"
        target="_blank"
        @click.stop
      >
        <UiItem
          class="ui-clickable"
          :text="file.name"
          :secondary="file.size | bytes"
          :icon="'mime:'+file.mimetype"
        />
      </a>
    </div>

    <component
      v-else-if="column.component"
      :is="column.component"
      v-bind="columnProps"
    ></component>

    <span v-else-if="typeof value == 'string'">{{ value }}</span>

    <pre
      v-else
      class="ui-table-cell-default"
    >{{ value }}</pre>
  </div>
</template>

<script>
import useI18n from '../../../i18n/mixins/useI18n.js';
import { UiItem } from '../UiItem';
import bytes from '../../filters/bytes.js';

export default {
  name: 'TableCell',

  mixins: [useI18n],
  components: { UiItem },
  filters: { bytes },

  props: {
    value: {
      required: false,
      default: null,
    },

    column: {
      type: Object,
      required: false,
      default: () => ({
        type: 'string',
      }),
    },

    record: {
      type: [Object, Array],
      required: false,
      default: () => ({}),
    },
  },

  computed: {
    type() {
      return this.column.type || null;
    },

    columnProps() {
      let props = {};
      if (typeof this.column.props == 'function') {
        props = this.column.props(this.record, this.value);
      } else if (typeof this.column.props == 'object') {
        props = this.column.props;
      }

      return props;
    },

    enumValue() {
      if (!this.column.enum || this.value == null) {
        return null;
      }

      let foundItem = this.column.enum.find((item) => item.value == this.value);
      return foundItem
        ? foundItem
        : {
            text: this.value,
            value: this.value,
          };
    },
  },
};
</script>

<style lang="scss">
.ui-table-cell {
  white-space: nowrap;

  // &.type-date,
  // &.type-day,
  // &.type-timestamp {
  //   white-space: nowrap;
  // }

  .enum-value {
    padding: 2px 8px;
    border-radius: var(--ui-radius);
    font-family: var(--ui-font-secondary);
    font-size: 11px;

    background-color: var(--ui-color-primary);
    color: #fff;
  }
}
</style>