<template>
  <div
    class="CmsBlockLauncher"
    :class="{'--open': isOpen, '--closed': !isOpen}"
  >
    <UiPopover
      v-if="type == 'popover'"
      :open.sync="isOpen"
    >
      <template #trigger="{ isOpen }">
        <div class="launcher-trigger">
          <div class="spacer"></div>
          <UiIcon
            class="launcher-icon"
            :value="isOpen ? 'mdi:close-circle-outline' : 'mdi:plus-circle'"
          />
        </div>

      </template>
      <template #contents="{close}">
        <div class="popover-contents">
          <LauncherContents
            v-bind="$attrs"
            v-on="$listeners"
            @input="close()"
          />
        </div>
      </template>
    </UiPopover>
    <LauncherContents
      v-else
      v-bind="$attrs"
      v-on="$listeners"
    />
  </div>
</template>

<script>
import LauncherContents from './LauncherContents.vue';
import { UiIcon, UiPopover } from '/packages/ui';

export default {
  name: 'CmsBlockLauncher',

  components: {
    LauncherContents,
    UiIcon,
    UiPopover,
  },

  props: {
    type: {
      type: String, // 'default' o 'popover'
      required: false,
      default: 'default',
    },
  },

  data() {
    return {
      isOpen: false,
    };
  },
};
</script>

<style lang="scss">
.popover-contents {
  min-width: 800px;
  min-height: 300px;
}

.CmsBlockLauncher {
  .launcher-trigger {
    position: relative;
    cursor: pointer;
    height: 32px;

    display: flex;
    align-items: center;
    justify-content: center;

    transition: all var(--ui-duration-snap);

    .spacer {
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      height: 1px;
      border-top: 3px dashed var(--ui-color-primary);

      transition: all var(--ui-duration-snap);
      opacity: 0;
    }

    .launcher-icon {
      --ui-icon-size: 28px;
      width: 28px;
      color: rgba(0, 0, 0, 0.3);
      transition: all var(--ui-duration-snap);
      border-radius: 50%;
      background-color: var(--ui-color-background);
      z-index: 2;
    }

    &:hover {
      background-color: rgba(0, 0, 0, 0.02);

      .spacer {
        opacity: 1;
      }

      .launcher-icon {
        color: var(--ui-color-primary);
      }
    }
  }

  &.--open {
    .launcher-trigger {
      .spacer {
        opacity: 1;
      }
      .launcher-icon {
        color: var(--ui-color-primary);
      }
    }
  }
}
</style>