const UiVideo = () => import('/packages/ui/components/UiVideo/UiVideo.vue')
const MediaVideoPicker = () => import('./MediaVideoPicker.vue')

export default {
  "id": "MediaVideo",
  "title": "Video",
  "icon": "g:slideshow",

  "block": {
    "component": UiVideo,
    "v-model": "video",
    "props": {
      "url": ""
    }
  },

  "editor": {
    "face": {
      "component": MediaVideoPicker,
      "v-model": "block.props.url"
    }
  }
}