<template>
  <div class="ui-infinite-scroll">
    <UiLoading :label="label" />
  </div>
</template>

<script>
// https://www.smashingmagazine.com/2018/01/deferring-lazy-loading-intersection-observer-api/
import { UiLoading } from '../UiLoading';
export default {
  name: 'UiInfiniteScroll',
  components: { UiLoading },

  props: {
    label: {
      type: String,
      required: false,
      default: 'Cargando ...',
    },
  },

  data() {
    return {
      isLeaving: false,
      IntersectionObserver: null,
    };
  },

  methods: {
    callbackObserver(entries) {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          // we are ENTERING the "capturing frame". Set the flag.
          this.isLeaving = true;
          // Do something with entering entry
          this.$emit('enter');
        } else if (this.isLeaving) {
          // we are EXITING the "capturing frame"
          this.isLeaving = false;
          // Do something with exiting entry
          this.$emit('leave');
        }
      });
    },
  },

  mounted() {
    this.IntersectionObserver = new IntersectionObserver(
      this.callbackObserver,
      {
        rootMargin: '0px 0px 0px 0px',
        threshold: 0.2,
      }
    );

    this.IntersectionObserver.observe(this.$el);
  },
};
</script>

<style lang="scss" >
.ui-infinite-scroll {
  height: 100px;
}
</style>