<template>
  <div :class="['cms-page-editor', {'--dragging': isDragging, '--layouting': isLayouting}]">
    <div class="page-editor-toolbar ui-toolbar --wide --clear">
      <slot name="toolbar"></slot>

      <h2 class="page-toolbar-title">{{ page.title }}</h2>

      <label class="ui-clickable preview-label ui-toolbar-item">
        <input
          type="checkbox"
          v-model="isPreview"
        >
        Vista previa
      </label>

      <UiDataDialog v-model="pageModel">
        <template #trigger>
          <UiIcon
            title="Modelo de datos"
            value="mdi:variable"
            class="ui-clickable"
          />
        </template>
        <template #contents="{innerModel, setModel}">
          <UiInputJson
            :value="innerModel"
            @input="setModel"
          />
        </template>
      </UiDataDialog>

      <!-- <select
        class="ui-clickable"
        v-model="page.theme"
        @change="emitInput"
      >
        <option value="default">Default</option>
        <option value="courier">Courier</option>
        <option value="newman">Newman</option>
      </select> -->

      <UiHistory
        class="ui-row history-controls"
        :name="`cms-page:${page.id}`"
        v-model="page"
        @input="emitInput"
      >
        <template #default="{hasUndo, hasRedo, undo, redo}">
          <UiIcon
            value="mdi:undo"
            @click="undo"
            class="ui-clickable history-icon"
            :class="{'--disabled': !hasUndo}"
          />
          <UiIcon
            value="mdi:redo"
            @click="redo"
            class="ui-clickable history-icon"
            :class="{'--disabled': !hasRedo}"
          />
        </template>
      </UiHistory>

      <UiIcon
        class="ui-clickable icon-layouting"
        title="Editar columnas"
        value="mdi:table-border"
        @click="isLayouting = !isLayouting"
      />

      <UiIcon
        class="ui-clickable icon-settings"
        title="Opciones"
        value="mdi:cog"
        @click="isSettingsOpen = !isSettingsOpen"
      />

      <slot name="toolbar-end"></slot>
    </div>

    <div class="page-editor-preview">
      <CmsPage
        v-if="isPreview"
        :value="page"
        :model.sync="pageModel"
      />
    </div>

    <div
      v-show="!isPreview"
      class="page-layout cms-theme"
      :class="`--theme-${page.theme}`"
    >
      <div
        v-for="(row, rowIndex) in computedLayout"
        :key="rowIndex"
        class="layout-row-wrapper"
      >
        <LayoutEditor
          v-if="isLayouting"
          class="layout-editor"
          :row-index="rowIndex"
          :value="page"
          @input="emitInput"
        />

        <div class="page-layout-row">
          <div
            v-for="(column, columnIndex) in row.columns"
            :key="columnIndex"
            class="page-layout-column"
            :style="{flex: column.flex}"
          >
            <!-- style="height: 100%" -->
            <draggable
              :value="column.blocks"
              @input="draggableInput(rowIndex, columnIndex, $event)"
              @update="draggableFix(rowIndex, columnIndex, $event)"
              @add="draggableFix(rowIndex, columnIndex, $event)"
              @start="isDragging = true"
              @end="isDragging = false"
              handle=".block-drag-handle"
              group="column-draggable"
              :swap-threshold="0.5"
              :animation="150"
            >
              <div
                class="block-editor-container"
                v-for="block in column.blocks"
                :key="block.pageIndex"
              >
                <CmsBlockEditor
                  :data-block-index="block.pageIndex"
                  class="block-editor"
                  v-model="page.blocks[block.pageIndex]"
                  @input="emitInput"
                  @delete="deleteBlock(block)"
                />

                <CmsBlockLauncher
                  type="popover"
                  class="minilauncher"
                  :page="page"
                  :hide-tabs="hideTabs"
                  :settings="settings"
                  @input="addBlock($event, block.layout.row, block.layout.column, block.layout.order)"
                />
              </div>
            </draggable>

            <CmsBlockLauncher
              v-if="!column.blocks.length"
              type="default"
              class="page-launcher"
              :page="page"
              :hide-tabs="hideTabs"
              :settings="settings"
              @input="addBlock($event, rowIndex, columnIndex)"
            />
          </div>
        </div>
      </div>
    </div>

    <UiPanel
      :open="isSettingsOpen"
      @close="isSettingsOpen = false"
    >
      <PagePanel
        v-model="page"
        @input="emitInput"
      />
    </UiPanel>
  </div>
</template>

<script>
import draggable from 'vuedraggable';

import PagePanel from './PagePanel.vue';
import {
  UiHistory,
  UiIcon,
  UiPanel,
  UiDataDialog,
  UiInputJson,
} from '/packages/ui';

import CmsPage from '../CmsPage/CmsPage.vue';
import CmsBlockEditor from '../CmsBlockEditor/CmsBlockEditor.vue';
import CmsBlockLauncher from '../CmsBlockLauncher/CmsBlockLauncher.vue';
import LayoutEditor from './LayoutEditor.vue';

import helperPage from '../../helpers/Page.js';

export default {
  name: 'CmsPageEditor',
  components: {
    CmsPage,
    draggable,
    PagePanel,

    UiHistory,
    UiIcon,
    UiPanel,

    UiDataDialog,
    UiInputJson,
    CmsBlockEditor,
    CmsBlockLauncher,
    LayoutEditor,
  },

  provide() {
    return {
      CmsPageRoot: this.CmsPageRoot,
    };
  },

  props: {
    value: {
      type: Object,
      required: false,
      default: null,
    },

    hideTabs: {
      type: Array,
      required: false,
      default: () => [],
    },

    settings: {
      type: Object,
      required: false,
      default: () => ({}),
    },
  },

  data() {
    return {
      page: null,
      isSettingsOpen: false,
      isDragging: false,
      isLayouting: false,

      isPreview: false,
      pageModel: {
        foo: 'foo',
      },

      CmsPageRoot: {
        schema: this.schema,
      },
    };
  },

  watch: {
    value: {
      immediate: true,
      deep: true,
      handler(newValue) {
        this.page = this.sanitizePage(newValue);
        this.CmsPageRoot.schema = helperPage.getDataSchema(this.page);
      },
    },
  },

  computed: {
    computedLayout() {
      let layout = JSON.parse(JSON.stringify(this.page.layout));

      this.page.blocks.forEach((block) => {
        layout[block.layout.row].columns[block.layout.column].blocks.push(
          block
        );
      });

      layout.forEach((row) => {
        row.columns.forEach((column) => {
          column.blocks.sort((a, b) => a.layout.order - b.layout.order);
        });
      });

      return layout;
    },
  },

  methods: {
    sanitizePage(incomingPage) {
      if (!incomingPage) {
        return {
          layout: [
            {
              columns: [
                {
                  flex: 1,
                  blocks: [],
                },
              ],
            },
          ],
          blocks: [],
          theme: 'default',
        };
      }

      let page = JSON.parse(JSON.stringify(incomingPage));
      if (!page.layout) {
        page.layout = [{ columns: [{ flex: 1 }] }];
      }

      if (!page.theme) {
        page.theme = 'default';
      }

      // make sure every layout column has a "blocks" array
      page.layout.forEach((row) => {
        row.columns.forEach((column) => {
          if (!column.blocks) {
            column.blocks = [];
          }
        });
      });

      // make sure the layout contains every row and column specified in block layout
      page.blocks.forEach((block, blockIndex) => {
        block.pageIndex = blockIndex;
        block.layout = Object.assign(
          { row: 0, column: 0, order: blockIndex },
          block.layout
        );

        if (!page.layout[block.layout.row]) {
          page.layout[block.layout.row] = { columns: [] };
        }

        if (!page.layout[block.layout.row].columns[block.layout.column]) {
          page.layout[block.layout.row].columns[block.layout.column] = {
            flex: 1,
            blocks: [],
          };
        }

        if (
          !page.layout[block.layout.row].columns[block.layout.column].blocks
        ) {
          page.layout[block.layout.row].columns[
            block.layout.column
          ].blocks = [];
        }
      });

      return page;
    },

    draggableInput(rowIndex, columnIndex, blocks) {
      let order = 0;
      blocks.forEach((block) => {
        this.page.blocks[block.pageIndex].layout.row = rowIndex;
        this.page.blocks[block.pageIndex].layout.column = columnIndex;
        this.page.blocks[block.pageIndex].layout.order = order++;
      });
    },

    draggableFix(rowIndex, columnIndex, event) {
      // fix vuedraggable bug:  on first interaction, item always lands at index 0 !!!
      let block = this.page.blocks[event.item.getAttribute('data-block-index')];

      if (block && block.layout.order != event.newIndex) {
        this.computedLayout[rowIndex].columns[columnIndex].blocks.forEach(
          (bl, i) => {
            if (bl.pageIndex == block.pageIndex) {
              bl.layout.order = event.newIndex;
            } else if (i <= event.newIndex) {
              bl.layout.order--;
            }
          }
        );
      }

      this.emitInput();
    },

    addBlock(newBlock, rowIndex, columnIndex, order = null) {
      // Si se agrega un bloque de texto (MediaHtml), y el ultimo bloque de la columna
      // es tambien un bloque de texto (MediaHtml), concatenar el contenido
      let columnBlocks = this.computedLayout[rowIndex].columns[columnIndex]
        .blocks;

      let targetOrder = order == null ? columnBlocks.length : order + 1;

      let previousBlock =
        targetOrder >= 1 ? columnBlocks[targetOrder - 1] : null;

      if (
        newBlock._suggested &&
        newBlock.component == 'MediaHtml' &&
        previousBlock &&
        previousBlock.component == newBlock.component
      ) {
        previousBlock.props.value += newBlock.props.value;
        this.emitInput();
        return;
      }

      newBlock.layout = {
        row: rowIndex,
        column: columnIndex,
        order: targetOrder,
      };

      delete newBlock._suggested;

      // push all next blocks forward
      this.page.blocks
        .filter(
          (b) =>
            b.layout.row == rowIndex &&
            b.layout.column == columnIndex &&
            b.layout.order >= targetOrder
        )
        .forEach((b) => b.layout.order++);

      this.page.blocks.push(newBlock);
      this.emitInput();
    },

    deleteBlock(block) {
      this.page.blocks.splice(block.pageIndex, 1);
      this.emitInput();
    },

    emitInput() {
      this.page = this.sanitizePage(this.page);
      this.$emit('input', JSON.parse(JSON.stringify(this.page)));
    },
  },
};
</script>

<style lang="scss">
.cms-page-editor {
  .preview-label {
    display: flex;
    align-items: center;
    padding: 0 var(--ui-padding-horizontal);

    input {
      margin-right: var(--ui-breathe);
    }
  }

  .block-editor-container {
    position: relative;

    .block-editor {
      position: relative;
      z-index: 2;
    }

    .minilauncher {
      z-index: 0;
      position: absolute;
      left: 0;
      right: 0;
      top: 100%;
      margin-top: -0.5em;

      opacity: 0;
      transition: opacity var(--ui-duration-snap);

      &:hover,
      &.--open {
        z-index: 3;
      }
    }

    &:hover .minilauncher,
    .minilauncher.--open {
      opacity: 1;
    }
  }

  .page-editor-toolbar {
    .page-toolbar-title {
      flex: 1;
      font-size: 1em;
      color: rgba(255, 255, 255, 0.7);
      align-self: center;
      padding: 0 12px;
      justify-content: left;
    }
  }

  .history-controls {
    .history-icon {
      &.--disabled {
        opacity: 0.5;
        pointer-events: none;
      }
    }
  }

  .page-layout-row {
    display: flex;
    min-height: 32px;
  }

  .page-launcher {
    border-radius: var(--ui-radius);
    border: 3px dashed var(--ui-color-primary);
  }

  .sortable-ghost {
    border-color: transparent !important;
    background-color: rgba(0, 0, 0, 0.1) !important;

    & > * {
      visibility: hidden;
    }
  }

  .page-editor-scrim {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    background-color: rgba(0, 0, 0, 0.2);
    z-index: 2;
    pointer-events: none;
  }

  &.--dragging {
    .page-layout-column {
      border: 1px dotted var(--ui-color-primary);
    }

    .block-editor {
      background-color: initial;
      border-color: transparent;

      // clase definida internamente en <block-editor> !
      .block-editor-toolbar {
        display: none;
      }
    }
  }

  .layout-row-wrapper {
    position: relative;
  }

  .layout-editor {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    // z-index: 2;
  }

  &.--layouting {
    .icon-layouting {
      color: #fff;
    }

    .page-layout-column {
      border: 2px dashed var(--ui-color-primary);
      border-top: 0;
      border-left-width: 0;

      &:first-child {
        border-left-width: 2px;
      }

      & > * {
        pointer-events: none;
        opacity: 0.3;
      }
    }
  }
}

@media (max-width: 800px) {
  .cms-page-editor {
    .page-layout-row {
      display: block;
    }
  }
}
</style>