const UiInput = () => import('../../UiInput/UiInput.vue')

export default {
  'date.eq': {
    text: 'es igual a',
    component: UiInput,
    props: (schema) => ({
      type: 'date',
      format: 'timestamp',
      ...schema.props
    }),
  },

  'date.gt': {
    text: 'es mayor que',
    component: UiInput,
    props: { type: 'date', format: 'timestamp' }
  },

  'date.gte': {
    text: 'es mayor o igual que',
    component: UiInput,
    props: { type: 'date', format: 'timestamp' }
  },

  'date.lt': {
    text: 'es menor que',
    component: UiInput,
    props: { type: 'date', format: 'timestamp' }
  },

  'date.lte': {
    text: 'es menor o igual que',
    component: UiInput,
    props: { type: 'date', format: 'timestamp' }
  },

  'date.between': {
    text: 'está entre',
    component: UiInput,
    props: { type: 'date', format: 'timestamp' }
  },

  'date.logic': {
    text: 'Operacion de fechas',
    component: UiInput,
    props: { type: 'json' },
  },
}