<template>
  <div class="cms-validation-editor">

    <div
      class="ui-row --tight --top"
      v-for="(rule,i) in innerValue"
      :key="i"
    >
      <ui-data-drawer
        v-model="innerValue[i]"
        @input="emitInput"
      >
        <template #trigger>
          <ui-item
            class="ui-clickable"
            v-bind="rule"
          ></ui-item>
        </template>

        <template #contents="{innerModel, setModel}">
          <rule-editor
            :value="innerModel"
            @input="setModel"
          ></rule-editor>
        </template>
      </ui-data-drawer>

      <ui-icon
        value="mdi:close"
        class="ui-clickable"
        @click="removeRule(i)"
      ></ui-icon>
    </div>

    <ui-data-drawer @input="addRule">
      <template #trigger>
        <ui-item
          class="ui-clickable"
          icon="mdi:plus"
          text="Agregar condición"
        ></ui-item>
      </template>
      <template #contents="{setModel}">
        <rule-editor @input="setModel"></rule-editor>
      </template>
    </ui-data-drawer>

  </div>
</template>

<script>
import RuleEditor from './RuleEditor.vue';
import { UiItem, UiIcon, UiDataDrawer } from '/packages/ui';

export default {
  name: 'cms-validation-editor',
  components: { UiItem, UiIcon, RuleEditor, UiDataDrawer },

  props: {
    value: {
      type: Array,
      required: false,
      default: null,
    },
  },

  data() {
    return {
      innerValue: [],
    };
  },

  watch: {
    value: {
      immediate: true,
      handler(newValue) {
        this.innerValue = this.sanitizeRules(newValue);
      },
    },
  },

  methods: {
    addRule(rule) {
      this.innerValue.push(rule);
      this.emitInput();
    },

    removeRule(index) {
      this.innerValue.splice(index, 1);
      this.emitInput();
    },

    sanitizeRules(arrRules) {
      return Array.isArray(arrRules)
        ? JSON.parse(JSON.stringify(arrRules))
        : [];
    },

    emitInput() {
      this.$emit('input', JSON.parse(JSON.stringify(this.innerValue)));
    },
  },
};
</script>