<template>
  <div class="ui-calendar-controls">
    <!-- <input type="text" :value="date" @input="$emit('update:date', $event.target.value)" />
    <input type="text" :value="view" @input="$emit('update:view', $event.target.value)" />-->

    <button
      type="button"
      class="ui-button"
      @click="setToday()"
    >Hoy</button>

    <span class="view-selector">
      <select
        class="ui-native"
        :value="currentView"
        @change="setCurrentView($event.target.value)"
      >
        <option value="month">{{ $t('UiCalendar.Month') }}</option>
        <option value="week">{{ $t('UiCalendar.Week') }}</option>
        <option value="schedule">{{ $t('UiCalendar.Schedule') }}</option>
        <option value="days">{{ $t('UiCalendar.Days') }}</option>
      </select>
    </span>

    <span
      class="counter-days"
      v-if="currentView == 'days'"
    >
      <span @click="setCurrentDay(0)">
        <UiIcon value="mdi:minus"></UiIcon>
      </span>
      {{ currentDay }} días
      <span @click="setCurrentDay(1)">
        <UiIcon value="mdi:plus"></UiIcon>
      </span>
    </span>

    <span class="month-selector">
      <select
        class="ui-native"
        :value="innerDate.getMonth()"
        @change="setMonth($event.target.value)"
      >
        <option value="0">Enero</option>
        <option value="1">Febrero</option>
        <option value="2">Marzo</option>
        <option value="3">Abril</option>
        <option value="4">Mayo</option>
        <option value="5">Junio</option>
        <option value="6">Julio</option>
        <option value="7">Agosto</option>
        <option value="8">Septiembre</option>
        <option value="9">Octubre</option>
        <option value="10">Noviembre</option>
        <option value="11">Diciembre</option>
      </select>
      <span class="current-year">{{ innerDate.getFullYear() }}</span>
    </span>

    <span class="month-controls">
      <span
        class="month-prev"
        @click="goPrev()"
      >
        <UiIcon value="g:keyboard_arrow_left"></UiIcon>
      </span>

      <span
        class="month-next"
        @click="goNext()"
      >
        <UiIcon value="g:keyboard_arrow_right"></UiIcon>
      </span>
    </span>
  </div>
</template>

<script>
// Debe recibir dos props:
// "date": una fecha "actual"/"fecha seleccionada"
// "view": una cadena indicando la vista seleccionada

// Debe mostrar controles para cambiar la vista ("view") y la fecha ("date")
// y cuando lo haga debe
// Emitir un evento update:date cuando cambie la fecha
// Emitir un evento update:view cuando cambie la vista

// (Estas dos cosas las pueden copiar de src\modules\ui\components\Calendar\Calendar.vue)

import useI18n from '../../../i18n/mixins/useI18n.js';
import { UiIcon } from '../UiIcon';

export default {
  name: 'UiCalendarControls',
  mixins: [useI18n],

  components: {
    UiIcon
  },

  props: {
    date: {
      type: Date,
      required: false,
      default: () => new Date()
    },

    view: {
      type: String,
      required: false,
      default: 'month'
    },

    day: {
      type: Number,
      required: false,
      default: 7
    }
  },

  data() {
    return {
      innerDate: null,
      currentView: this.view,
      currentDay: this.day
    };
  },

  watch: {
    date: {
      immediate: true,
      handler(newVal) {
        this.innerDate = newVal;
      }
    }
  },

  methods: {
    setToday() {
      this.innerDate = new Date();
      this.$emit('update:date', this.innerDate);
    },

    setCurrentView(view) {
      this.currentView = view;
      this.$emit('update:view', this.currentView);
    },

    setCurrentDay(type) {
      if (type) {
        if (this.currentDay < 30) {
          this.currentDay++;
        }
      } else {
        if (this.currentDay > 1) {
          this.currentDay--;
        }
      }

      this.$emit('update:day', this.currentDay);
    },

    setMonth(monthNum) {
      // see https://github.com/vuejs/vue/issues/3613
      // and https://stackoverflow.com/questions/55653149/vuejs-reactive-date-object
      this.innerDate.setMonth(monthNum, 1);
      this.innerDate = new Date(this.innerDate);
      this.$emit('update:date', this.innerDate);
    },

    goNext() {
      let counter = 7;

      if (this.currentView == 'days') {
        counter = this.currentDay;
      }

      if (this.currentView == 'week' || this.currentView == 'days') {
        this.innerDate.setDate(this.innerDate.getDate() + counter);
        this.innerDate = new Date(this.innerDate);
        this.$emit('update:date', this.innerDate);
        return;
      }

      this.setMonth(this.innerDate.getMonth() + 1);
    },

    goPrev() {
      let counter = 7;

      if (this.currentView == 'days') {
        counter = this.currentDay;
      }

      if (this.currentView == 'week' || this.currentView == 'days') {
        this.innerDate.setDate(this.innerDate.getDate() - counter);
        this.innerDate = new Date(this.innerDate);
        this.$emit('update:date', this.innerDate);
        return;
      }

      this.setMonth(this.innerDate.getMonth() - 1);
    }
  },

  i18n: {
    en: {
      'UiCalendar.Month': 'Month',
      'UiCalendar.Week': 'Week',
      'UiCalendar.Schedule': 'Schedule',
      'UiCalendar.Days': 'Days'
    },

    es: {
      'UiCalendar.Month': 'Mes',
      'UiCalendar.Week': 'Semana',
      'UiCalendar.Schedule': 'Agenda',
      'UiCalendar.Days': 'Día'
    }
  }
};
</script>

<style lang="scss">
.ui-calendar-controls {
  display: flex;
  align-items: center;
  padding: var(--ui-breathe);
  border-radius: var(--ui-radius);
  background-color: #fff;

  .view-selector {
    margin: 0 12px;
  }

  .month-selector {
    flex: 1;
    text-align: right;
    margin: 0 12px;

    select {
      margin: 0 12px;
    }
  }

  .month-controls,
  .counter-days {
    display: flex;
    align-items: center;
    margin: 0 12px;

    & > span {
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;

      height: 40px;
      width: 40px;

      border-radius: 50%;
      background-color: transparent;
      color: #666;

      &:hover {
        color: #222;
        background-color: rgba(0, 0, 0, 0.06);
      }
    }
  }
}
</style>