const UiInput = () => import('../../UiInput/UiInput.vue')

export default {
  'array.hasAny': {
    text: 'contiene alguno',
    component: UiInput,
    props: (schema) => ({
      type: 'select',
      multiple: true,
      native: false,
      options: schema.items ? schema.items.enum : [],
      ...schema.props
    }),

    redact(condition) {
      if (!condition || !condition.args) {
        return `Contiene alguno`

      }
      return `Contiene alguno de ${condition.args.length} elementos`
    }
  },

  'array.hasAll': {
    text: 'contiene todos',
    component: UiInput,
    props: (schema) => ({
      type: 'select',
      multiple: true,
      options: schema.items ? schema.items.enum : [],
      ...schema.props
    }),
  },

  'array.empty': {
    text: 'está vacío',
  },

  'array.nempty': {
    text: 'no está vacío',
  },

  // 'array.length.eq': {
  //   text: 'cantidad de elementos',
  // },
}