<template>
  <div class="ui-condition-editor">
    <ConditionGroup
      v-if="condition.op == 'or' || condition.op == 'and'"
      :schema="fullSchema"
      v-model="condition"
      @input="emitInput"
      :custom-conditions="customConditions"
    />
    <ConditionPrimitive
      v-else
      :schema="fullSchema"
      v-model="condition"
      @input="emitInput"
      :custom-conditions="customConditions"
    />
  </div>
</template>

<script>
import { sanitizeCondition } from '../../../validation';
import { schemaMerge } from './functions/schema';
import ConditionGroup from './ConditionGroup.vue';
import ConditionPrimitive from './ConditionPrimitive.vue';

export default {
  name: 'UiConditionEditor',
  components: { ConditionGroup, ConditionPrimitive },

  inject: {
    UiConditionEditor: { default: null },
  },

  props: {
    value: {
      type: Object,
      required: false,
      default: null,
    },

    schema: {
      type: Object,
      required: false,
      default: null,
    },

    customConditions: {
      type: Object,
      required: false,
      default: null,
    },
  },

  data() {
    return {
      condition: null,

      blankCondition: {
        field: null,
        op: 'and',
        args: null,
      },
    };
  },

  computed: {
    fullSchema() {
      let retval = {
        type: 'object',
        props: {},
      };

      // Buscar un esquema injectado mediante "inject:UiConditionEditor"
      if (this.UiConditionEditor && this.UiConditionEditor.schema) {
        schemaMerge(retval, this.UiConditionEditor.schema);
      }

      // Esquema agregado como argumentos
      schemaMerge(retval, this.schema);

      return retval;
    },
  },

  watch: {
    value: {
      immediate: true,
      handler(newValue) {
        this.condition = sanitizeCondition(newValue) || this.blankCondition;
      },
    },
  },

  methods: {
    emitInput() {
      this.$emit('input', JSON.parse(JSON.stringify(this.condition)));
    },
  },
};
</script>