<template>
  <component
    class="VmExpression"
    :is="editor.component"
    v-bind="editor.props"
    :value="value"
    @input="$emit('input', $event)"
  ></component>
</template>

<script>
import { UiInputJson } from '../../../ui';

const StmtBoo = () => import('./statements/boo/StmtBoo.vue');
const StmtCall = () => import('./statements/call/StmtCall.vue');
const StmtChain = () => import('./statements/chain/StmtChain.vue');
const StmtDo = () => import('./statements/do/StmtDo.vue');
const StmtFunction = () => import('./statements/function/StmtFunction.vue');
const StmtIf = () => import('./statements/if/StmtIf.vue');
const StmtNot = () => import('./statements/not/StmtNot.vue');
const StmtOp = () => import('./statements/op/StmtOp.vue');

export default {
  name: 'VmExpressionInternal',
  inject: ['VmExpressionRoot'],

  props: {
    value: {
      required: false,
      default: null,
    },
  },

  computed: {
    editor() {
      if (this.value == null || typeof this.value != 'object') {
        return { component: UiInputJson };
      }

      if (Array.isArray(this.value)) {
        return { component: UiInputJson };
      }

      if (typeof this.value.function != 'undefined') {
        return { component: StmtFunction };
      }

      if (typeof this.value.do != 'undefined') {
        return { component: StmtDo };
      }

      if (typeof this.value.if != 'undefined') {
        return { component: StmtIf };
      }

      if (typeof this.value.not != 'undefined') {
        return { component: StmtNot };
      }

      if (typeof this.value.call != 'undefined') {
        let fnName = this.value.call;

        if (
          fnName &&
          this.VmExpressionRoot.functions[fnName] &&
          this.VmExpressionRoot.functions[fnName].editor
        ) {
          return this.VmExpressionRoot.functions[fnName].editor;
        }

        return { component: StmtCall };
      }

      if (typeof this.value.chain != 'undefined') {
        return { component: StmtChain };
      }

      if (typeof this.value.op != 'undefined') {
        return { component: StmtOp };
      }

      if (Array.isArray(this.value.and) || Array.isArray(this.value.or)) {
        return { component: StmtBoo };
      }

      return { component: UiInputJson };
    },
  },
};
</script>