import Html from './Quicklaunch/Html.js'
import Input from './Quicklaunch/Input.js'
import Select from './Quicklaunch/Select.js'
import Video from './Quicklaunch/Video.js'
import Link from './Quicklaunch/Link.js'
const generators = [Select, Input, Video, Link, Html];

export default {
  getSuggestedBlocks(text) {
    let retval = [];
    let string = text.trim();
    if (!string) {
      return retval;
    }

    generators.forEach(generator => {
      let generated = generator(string);
      if (generated) {
        retval.push(generated);
      }
    });

    return retval;
  }
}