<template>
  <div class="cms-validation-rule-editor">
    <input
      type="text"
      class="ui-native rule-text"
      v-model="innerValue.text"
      @input="emitInput"
    >

    <UiConditionEditor
      v-model="innerValue.rule"
      @input="emitInput"
    />
  </div>
</template>

<script>
import { UiConditionEditor } from '/packages/ui';

export default {
  name: 'cms-validation-rule-editor',
  components: { UiConditionEditor },

  props: {
    value: {
      type: Object,
      required: false,
      default: null,
    },
  },

  data() {
    return {
      innerValue: {},
    };
  },

  watch: {
    value: {
      immediate: true,
      handler(newValue) {
        this.innerValue = this.sanitizeRule(newValue);
      },
    },
  },

  methods: {
    sanitizeRule(incomingRule) {
      let retval = incomingRule ? JSON.parse(JSON.stringify(incomingRule)) : {};
      return Object.assign(
        {
          text: null,
          rule: null,
        },
        retval
      );
    },

    emitInput() {
      this.$emit('input', JSON.parse(JSON.stringify(this.innerValue)));
    },
  },
};
</script>

<style lang="scss">
.cms-validation-rule-editor {
  .rule-text {
    width: 100%;
  }
}
</style>