<template>
  <component
    v-if="component"
    :is="component"
    v-bind="$attrs"
    v-on="$listeners"
  >
    <slot></slot>
  </component>
</template>

<script>
const GoogleMap = () => import('./providers/Google/Map.vue');
const MapboxMap = () => import('./providers/Mapbox/Map.vue');

export default {
  name: 'UiMap',

  components: {
    GoogleMap,
    MapboxMap,
  },

  props: {
    type: {
      required: false,
      default: 'google',
    },
  },

  computed: {
    component() {
      let name =
        this.type.charAt(0).toUpperCase() + this.type.slice(1).toLowerCase();
      return `${name}Map`;
    },
  },

  provide() {
    return {
      baseName: () => this.component,
    };
  },
};
</script>