<template>
  <div class="cms-block-listeners-editor">
    <div
      v-for="(event) in events"
      :key="event.name"
    >
      <label>{{ event.name }}</label>
      <VmExpression
        :schema="CmsPageRoot.schema"
        :value="event.expression"
        @input="setEvent(event.name, $event)"
      />
    </div>

    <UiDataDrawer @input="setEvent($event)">
      <template #trigger>
        <UiItem
          icon="mdi:plus"
          text="Agregar evento"
          class="ui-clickable"
        ></UiItem>
      </template>
      <template #contents="{setModel}">
        <input
          type="text"
          class="ui-native"
          @input="setModel($event.target.value)"
        />
      </template>
    </UiDataDrawer>

  </div>
</template>

<script>
import { VmExpression } from '/packages/vm/components';
import { UiItem, UiDataDrawer } from '/packages/ui';

export default {
  name: 'cms-block-listeners-editor',
  inject: ['CmsPageRoot'],
  components: { VmExpression, UiItem, UiDataDrawer },

  props: {
    value: {
      type: Object,
      required: false,
      default: null,
    },
  },

  data() {
    return {
      innerValue: null,
    };
  },

  watch: {
    value: {
      immediate: true,
      handler(newValue) {
        this.innerValue = this.sanitizeValue(newValue);
      },
    },
  },

  computed: {
    events() {
      let retval = [];
      Object.keys(this.innerValue).forEach((eventName) => {
        retval.push({
          name: eventName,
          expression: this.innerValue[eventName],
        });
      });

      return retval;
    },
  },

  methods: {
    sanitizeValue(newValue) {
      return newValue ? JSON.parse(JSON.stringify(newValue)) : {};
    },

    emitInput() {
      this.$emit('input', JSON.parse(JSON.stringify(this.innerValue)));
    },

    setEvent(eventName, eventAction = null) {
      let event = eventName.trim();
      if (!event) {
        return;
      }

      this.$set(this.innerValue, event, eventAction);
      this.emitInput();
    },
  },
};
</script>