<template>
  <div class="UiVideoRecorder">

    <button
      v-show="!stream"
      type="button"
      @click="startStream()"
    >turn camera ON</button>
    <button
      v-show="!!stream"
      type="button"
      @click="stopStream()"
    >turn camera off</button>

    <!-- preview -->
    <div v-show="!finalSrc">
      <video
        autoplay
        muted
        :srcObject.prop="stream"
        style="display:block; width: 320px; height: 240px;"
      ></video>
      <button
        v-show="!isRecording"
        type="button"
        id="startButton"
        class="ui-button --main"
        @click="startRecording()"
      >REC</button>
      <button
        v-show="isRecording"
        type="button"
        class="ui-button --cancel"
        id="stopButton"
        @click="stopRecording()"
      >Stop</button>

      <p>{{isRecording ? 'grabando' : 'idle' }}</p>
    </div>

    <!-- result -->
    <div v-show="!!finalSrc">
      <video
        controls
        :src="finalSrc"
        style="display:block; width: 320px; height: 240px;"
      ></video>
      <a
        id="downloadButton"
        class="ui-button --main"
        :href="finalSrc"
        download="VideoGrabado.webm"
      >
        Download
      </a>
      <button
        type="button"
        class="ui-button --cancel"
        @click="finalSrc = null"
      >Restart</button>

    </div>
  </div>

</template>

<script>
export default {
  data() {
    return {
      isRecording: false,

      stream: null,
      recorder: null,
      finalSrc: null,
    };
  },

  mounted() {
    this.startStream();
  },

  methods: {
    startStream() {
      return navigator.mediaDevices
        .getUserMedia({
          video: true,
          audio: true,
        })
        .then((stream) => (this.stream = stream));
    },

    stopStream() {
      if (!this.stream) {
        return;
      }

      this.stream.getTracks().forEach((track) => track.stop());
      this.stream = null;
    },

    startRecording() {
      this.finalSrc = null;
      this.recorder = new MediaRecorder(this.stream);
      this.recorder.start();

      this.isRecording = true;
    },

    stopRecording() {
      this.recorder.ondataavailable = (event) => {
        this.finalSrc = URL.createObjectURL(event.data);
      };

      this.recorder.stop();
      this.isRecording = false;
    },
  },
};
</script>