<template>
  <div class="condition-group">

    <div class="group-legend">
      <select
        v-model="condition.op"
        @change="emitInput"
      >
        <option value="and">Todas las siguientes</option>
        <option value="or">Cualquiera de las siguientes</option>
      </select>
    </div>

    <div class="group-conditions">
      <div
        v-for="(subcondition, i) in condition.args"
        :key="i"
        class="group-subcondition"
      >
        <ConditionGroup
          v-if="subcondition.op == 'or' || subcondition.op == 'and'"
          v-model="condition.args[i]"
          :schema="schema"
          :custom-conditions="customConditions"
          @input="emitInput"
        />
        <ConditionPrimitive
          v-else
          v-model="condition.args[i]"
          :schema="schema"
          :custom-conditions="customConditions"
          :open="pristine.includes(i)"
          @input="onPrimitiveInput(i)"
          @cancel="onPrimitiveCancel(i)"
        />

        <UiIcon
          class="icon-remove"
          value="mdi:close"
          @click="removeIndex(i)"
        />
      </div>
    </div>

    <div class="group-footer">
      <ConditionLauncher
        :schema="schema"
        @input="pushCondition"
        :custom-conditions="customConditions"
      />
    </div>
  </div>
</template>

<script>
import ConditionPrimitive from './ConditionPrimitive.vue';
import ConditionLauncher from './ConditionLauncher.vue';
import { UiIcon } from '../UiIcon';

export default {
  name: 'ConditionGroup',
  components: { ConditionPrimitive, ConditionLauncher, UiIcon },

  props: {
    value: {
      type: Object,
      required: true,
    },

    schema: {
      type: Object,
      required: false,
      default: null,
    },

    customConditions: {
      type: Object,
      required: false,
      default: null,
    },
  },

  data() {
    return {
      condition: {
        field: '',
        op: null,
        args: null,
      },

      pristine: [],
    };
  },

  watch: {
    value: {
      immediate: true,
      handler(newValue) {
        this.condition = JSON.parse(JSON.stringify(newValue));
      },
    },
  },

  methods: {
    onPrimitiveInput(conditionIndex) {
      this.pristine.splice(this.pristine.indexOf(conditionIndex), 1);
      this.emitInput();
    },

    onPrimitiveCancel(conditionIndex) {
      let isPristine = this.pristine.indexOf(conditionIndex) != -1;
      if (isPristine) {
        this.condition.args.splice(conditionIndex, 1);
        this.pristine.splice(this.pristine.indexOf(conditionIndex), 1);
      }
    },

    emitInput() {
      this.$emit('input', JSON.parse(JSON.stringify(this.condition)));
    },

    pushCondition(newCondition) {
      if (!Array.isArray(this.condition.args)) {
        this.condition.args = [];
      }
      this.condition.args.push(newCondition);
      this.pristine.push(this.condition.args.length - 1);
    },

    removeIndex(index) {
      this.condition.args.splice(index, 1);
      this.emitInput();
    },
  },
};
</script>

<style lang="scss">
.condition-group {
  border: 1px solid #ccc;
  border-radius: var(--ui-radius);

  .group-legend {
    padding: var(--ui-padding);

    select {
      padding: 0;
      background-color: transparent;
      border: none;
      font-family: var(--ui-font-secondary);
      font-weight: bold;
      font-size: 0.9em;
      outline-color: var(--ui-color-primary);

      cursor: pointer;
    }
  }

  .group-conditions {
    padding-left: 18px;

    .group-subcondition {
      position: relative;

      .icon-remove {
        position: absolute;
        top: 0;
        right: 0;
        opacity: 0.7;
        padding: var(--ui-padding);
        cursor: pointer;

        &:hover {
          color: var(--ui-color-danger);
          opacity: 1;
        }
      }
    }
  }

  .group-footer {
    padding: var(--ui-padding);
    background-color: var(--ui-color-darken);
  }
}
</style>