import apiClient from '../libraries/Client.js'

export default {
  inject: {
    $apiClient: {
      default: null
    }
  },

  computed: {
    $httpClient() {
      if (this.$apiClient) {
        if (typeof this.$apiClient == 'function') {
          return this.$apiClient();
        }
        return this.$apiClient;
      }

      return new apiClient();
    },

    $api() {
      if (!this.$options.$api && !this.$options.api) {
        return null;
      }

      let apiOptions = this.$options.$api ? this.$options.$api : this.$options.api;
      let httpClient = this.$httpClient;

      if (typeof apiOptions == 'function') {
        return apiOptions(httpClient);
      }

      if (typeof apiOptions == 'object') {
        let retval = {};
        for (let prop in apiOptions) {
          if (Object.prototype.hasOwnProperty.call(apiOptions, prop)) {
            retval[prop] = apiOptions[prop](httpClient);
          }
        }
        return retval;
      }

      return null;
    }
  }
}