<template>
  <div class="PageWatchEditor">
    <div
      class="watcher-item"
      v-for="(watcher, i) in innerValue"
      :key="i"
    >
      <label class="ui-label">{{ watcher.target }}</label>
      <button
        type="button"
        class="ui-button --cancel"
        @click="removeWatcher(i)"
      >&times;</button>

      <VmExpression
        v-model="innerValue[i].expr"
        :schema="CmsPageRoot.schema"
        @input="emitInput"
      />
    </div>

    <input
      type="text"
      class="ui-native"
      size="44"
      placeholder="Nombre de variable ..."
      @keyup.enter="addWatcher($event.target.value); $event.target.value = ''"
    />

  </div>
</template>

<script>
import { VmExpression } from '/packages/vm/components';

export default {
  name: 'PageWatchEditor',
  inject: ['CmsPageRoot'],
  components: { VmExpression },

  props: {
    value: {
      type: Array,
      required: false,
      default: null,
    },
  },

  data() {
    return {
      innerValue: [],
    };
  },

  watch: {
    value: {
      immediate: true,
      handler(newValue) {
        this.innerValue = Array.isArray(newValue)
          ? JSON.parse(JSON.stringify(newValue))
          : [];
      },
    },
  },

  methods: {
    removeWatcher(index) {
      this.innerValue.splice(index, 1);
      this.emitInput();
    },

    addWatcher(varname) {
      this.innerValue.push({
        target: varname,
        expr: { chain: [] },
      });

      this.emitInput();
    },

    emitInput() {
      this.$emit(
        'input',
        this.innerValue.length
          ? JSON.parse(JSON.stringify(this.innerValue))
          : null
      );
    },
  },
};
</script>