import { execute } from '../../../../functions';
import { UiFormGenerator } from '/packages/ui';

export default {
  id: 'google.sheets.saveRow',
  icon: 'mdi:google-spreadsheet',
  text: 'Google Sheet: Guardar fila',
  secondary: 'Sobreescribe una fila',
  schema: '... json-schema para el objeto "args"',

  editor: {
    component: UiFormGenerator,
    props: {
      schema: {
        fields: [
          {
            model: 'spreadsheetId',
            type: 'text',
            label: 'Sheet ID',
            props: {
              size: 80
            }
          },
          {
            model: 'body',
            type: 'json',
            label: 'Body'
          }
        ]
      }
    }
  },

  handler(args) {
    return execute({
      action: 'http',
      arguments: {
        method: 'PUT',
        url: `/1/google/sheets/sheet/${args.spreadsheetId}/record`,
        body: args.body
      }
    }, this);
  },

}