<template>
  <div class="ui-calendar ui-card ui-rounded">
    <UiCalendarControls
      :date.sync="innerDate"
      :view.sync="currentView"
      :day.sync="nDays"
      @update:date="$emit('update:date', $event)"
      @update:view="$emit('update:view', $event)"
    ></UiCalendarControls>

    <div class="ui-calendar-view">
      <template v-if="currentView == 'schedule'">
        <UiCalendarViewSchedule
          :date="innerDate"
          :events="sanitizedEvents"
          @click-day="$emit('click-day', $event)"
          @click-event="$emit('click-event', $event)"
        ></UiCalendarViewSchedule>
      </template>

      <template v-else-if="currentView == 'week'">
        <UiCalendarViewDays
          v-bind="$attrs"
          :n-days="7"
          :view="currentView"
          :date="innerDate"
          :events="sanitizedEvents"
          @click-day="$emit('click-day', $event)"
          @click-event="$emit('click-event', $event)"
        ></UiCalendarViewDays>
      </template>

      <template v-else-if="currentView == 'days'">
        <UiCalendarViewDays
          v-bind="$attrs"
          :n-days="nDays"
          :view="currentView"
          :date="innerDate"
          :events="sanitizedEvents"
          @click-day="$emit('click-day', $event)"
          @click-event="$emit('click-event', $event)"
        ></UiCalendarViewDays>
      </template>

      <template v-else>
        <UiCalendarViewMonth
          v-bind="$attrs"
          :date="innerDate"
          :events="sanitizedEvents"
          @click-day="$emit('click-day', $event)"
          @click-event="$emit('click-event', $event)"
        ></UiCalendarViewMonth>
      </template>
    </div>
  </div>
</template>

<script>
import { sanitizeEventAsObjects } from './functions.js';

import UiCalendarControls from './UiCalendarControls.vue';
import UiCalendarViewSchedule from './UiCalendarViewSchedule.vue';
import UiCalendarViewDays from './UiCalendarViewDays.vue';
import UiCalendarViewMonth from './UiCalendarViewMonth.vue';

export default {
  name: 'UiCalendar',

  components: {
    UiCalendarControls,
    UiCalendarViewSchedule,
    UiCalendarViewDays,
    UiCalendarViewMonth,
  },

  props: {
    date: {
      type: Date,
      required: false,
      default: () => new Date(),
    },

    events: {
      type: Array,
      required: false,
      default: () => [],
    },

    view: {
      type: String,
      required: false,
      default: 'month',
    },
  },

  data() {
    return {
      innerDate: null,
      currentView: this.view,
      nDays: 3,
    };
  },

  watch: {
    date: {
      immediate: true,
      handler(newVal) {
        this.innerDate = newVal;
      },
    },
  },

  computed: {
    sanitizedEvents() {
      return this.events.map(sanitizeEventAsObjects).filter((event, i) => {
        if (event == null) {
          console.warn('UiCalendar: Evento inválido', this.events[i]);
          return false;
        }
        return true;
      });
    },
  },
};
</script>

<style lang="scss">
.ui-calendar {
  .ui-calendar-header {
    display: flex;
    align-items: center;
    padding: var(--ui-breathe);
    border-radius: var(--ui-radius);
    background-color: #fff;
  }
}
</style>