<template>
  <UiPopover class="ui-button-selection">
    <template #trigger>
      <button
        type="button"
        class="ui-button main-button"
      >
        <UiIcon
          class="selection-icon"
          :value="icon"
          size="16"
        />
        <span
          class="selection-count"
          v-text="count"
        ></span>
        <UiIcon
          class="dropdown-icon"
          value="g:arrow_drop_down"
        />
      </button>
    </template>

    <template #contents="{toggle}">
      <UiList
        selectable
        @click.native="toggle()"
      >
        <UiItem
          v-for="action in actions"
          :key="action.label"
          class="ui-clickable"
          :text="action.label"
          @click="action.action()"
        />
      </UiList>
    </template>
  </UiPopover>
</template>

<script>
import UiIcon from '../UiIcon/UiIcon.vue';
import UiPopover from '../UiPopover/UiPopover.vue';
import UiList from '../UiList/UiList.vue';
import UiItem from '../UiItem/UiItem.vue';

export default {
  name: 'UiButtonSelection',

  components: {
    UiIcon,
    UiPopover,
    UiList,
    UiItem,
  },

  props: {
    /**
     * Cantidad de elementos seleccionados
     */
    count: {
      type: Number,
      required: false,
      default: 0,
    },

    /**
     * Total de elementos existentes (sirve para determinar si la cantidad en "count" es una seleccion total o parcial)
     */
    total: {
      type: Number,
      required: false,
      default: 0,
    },

    /**
     * Arreglo de acciones a mostrar en el menu popover
     */
    actions: {
      type: Array,
      required: false,
      default: () => [],
    },
  },

  computed: {
    icon() {
      if (!this.count) {
        return 'g:check_box_outline_blank';
      }

      if (!this.total || this.count == this.total) {
        return 'g:check_box';
      }

      return 'g:indeterminate_check_box';
    },
  },
};
</script>

<style lang="scss">
.ui-button-selection {
  display: inline-block;

  .main-button {
    .ui-icon {
      color: rgba(0, 0, 0, 0.7);
    }

    .selection-count {
      font-size: 1em;
      padding: 0 12px;
      line-height: 1em;
      font-weight: bold;
      color: rgba(0, 0, 0, 0.5);
    }
  }

  .dropdown-icon {
    height: 19px;
  }
}
</style>