<template>
  <UiDialog
    class="ui-data-dialog"
    v-bind="$attrs"
    @cancel="cancel"
  >
    <template #trigger="dialogBindings">
      <slot
        name="trigger"
        v-bind="{innerModel, setModel, ...dialogBindings}"
      ></slot>
    </template>
    <template #contents>
      <slot
        name="contents"
        v-bind="{innerModel, setModel}"
      ></slot>
    </template>
    <template #footer="{close}">
      <button
        type="button"
        class="ui-button --main"
        @click="accept(); close()"
      >Aceptar</button>
      <button
        type="button"
        class="ui-button --cancel"
        @click="cancel(); close()"
      >Cancelar</button>

      <slot
        name="footer"
        v-bind="{innerModel, setModel}"
      ></slot>
    </template>
  </UiDialog>
</template>

<script>
import { UiDialog } from '../UiDialog';

export default {
  name: 'UiDataDialog',
  components: { UiDialog },

  props: {
    value: {
      required: false,
      default: null,
    },
  },

  data() {
    return {
      innerModel: null,
    };
  },

  watch: {
    value: {
      immediate: true,
      deep: true,
      handler(newValue) {
        this.innerModel = this.sanitizeValue(newValue);
      },
    },
  },

  methods: {
    setModel(newValue) {
      this.innerModel = newValue;
    },

    sanitizeValue(newValue) {
      return newValue ? JSON.parse(JSON.stringify(newValue)) : null;
    },

    accept() {
      this.$emit('input', JSON.parse(JSON.stringify(this.innerModel)));
    },

    cancel() {
      this.innerModel = this.sanitizeValue(this.value);
      this.$emit('cancel');
    },
  },
};
</script>