<template>
  <div class="ui-audio-visualizer">
    <svg
      preserveAspectRatio="none"
      id="visualizer"
      version="1.1"
      xmlns="http://www.w3.org/2000/svg"
      xmlns:xlink="http://www.w3.org/1999/xlink"
    >
      <defs>
        <mask id="mask">
          <g id="maskGroup" />
        </mask>
        <linearGradient
          id="gradient"
          x1="0%"
          y1="0%"
          x2="0%"
          y2="100%"
        >
          <stop
            offset="0%"
            style="stop-color:#db6247;stop-opacity:1"
          />
          <stop
            offset="40%"
            style="stop-color:#f6e5d1;stop-opacity:1"
          />
          <stop
            offset="60%"
            style="stop-color:#5c79c7;stop-opacity:1"
          />
          <stop
            offset="85%"
            style="stop-color:#b758c0;stop-opacity:1"
          />
          <stop
            offset="100%"
            style="stop-color:#222;stop-opacity:1"
          />
        </linearGradient>
      </defs>
      <rect
        x="0"
        y="0"
        width="100%"
        height="100%"
        fill="url(#gradient)"
        mask="url(#mask)"
      />
    </svg>
  </div>
</template>

<script>
// Basado en
// https://github.com/apm1467/html5-mic-visualizer

export default {
  name: 'UiAudioVisualizer',

  props: {
    stream: {
      type: MediaStream,
      required: false,
      default: null
    }
  },

  data() {
    return {
      paths: null,
      analyser: null,
      frequencyArray: null,

      isPlaying: false
    };
  },

  watch: {
    stream: {
      // immediate: true,
      handler() {
        if (!this.stream) {
          this.stop();
          return;
        }
        this.createAnalyser();
      }
    }
  },

  mounted() {
    this.createAnalyser();
    this.stream && this.play();
  },

  methods: {
    createAnalyser() {
      this.paths = this.$el.getElementsByTagName('path');

      const audioContext = new AudioContext();
      const audioStream = audioContext.createMediaStreamSource(this.stream);

      this.analyser = audioContext.createAnalyser();
      this.analyser.fftSize = 1024;
      audioStream.connect(this.analyser);

      let bufferLength = this.analyser.frequencyBinCount;
      this.frequencyArray = new Uint8Array(bufferLength);

      for (let i = 0; i < bufferLength; i++) {
        let path = document.createElementNS(
          'http://www.w3.org/2000/svg',
          'path'
        );
        path.setAttribute('stroke-dasharray', '4,1');
        this.$el.querySelector('mask').appendChild(path);
      }
    },

    draw() {
      if (!this.isPlaying) {
        return;
      }

      let bufferLength = this.analyser.frequencyBinCount;
      this.analyser.getByteFrequencyData(this.frequencyArray);

      for (let i = 0; i < bufferLength; i++) {
        let adjustedLength =
          Math.floor(this.frequencyArray[i]) -
          (Math.floor(this.frequencyArray[i]) % 5);

        this.paths[i].setAttribute(
          'd',
          'M ' + i + ',255 l 0,-' + adjustedLength
        );
      }

      requestAnimationFrame(this.draw);
    },

    play() {
      this.isPlaying = true;
      this.draw();
    },

    stop() {
      this.isPlaying = false;
    }
  }
};
</script>

<style lang="scss">
.ui-audio-visualizer {
  svg {
    display: block;
    width: 100%;
    height: 200px;
    padding: 0;
    margin: 0;
  }

  path {
    stroke-linecap: square;
    stroke: white;
    stroke-width: 0.5px;
  }
}
</style>