<template>
  <div>
    <component
      v-if="options.custom"
      :is="options.custom"
      :definition="definition"
      :value="value"
      :class="{'--focused': isFocused}"
      @input="$emit('input', $event)"
      @delete="$emit('delete')"
      @focus="isFocused = true"
      @blur="isFocused = false"
    ></component>
    <EditorLayout
      v-else
      class="cms-block-editor"
      :class="{'--focused': isFocused}"
      :definition="definition"
      :value="value"
      @input="$emit('input', $event)"
      @delete="$emit('delete')"
    >
      <template #toolbar>
        <!-- Custom toolbar component -->
        <BlockEditorComponent
          v-if="options.toolbar"
          class="toolbar-component"
          :definition="options.toolbar"
          :value="value"
          @input="$emit('input', $event)"
        />
      </template>

      <div class="block-editor-face">
        <BlockEditorComponent
          v-if="options.face"
          ref="facecomponent"
          :definition="options.face"
          :value="value"
          @input="$emit('input', $event)"
          @focus="isFocused = true"
          @blur="isFocused = false"
        />
        <CmsBlockComponent
          v-else-if="definition && definition.block && definition.block.component"
          :name="definition.block.component"
          v-bind="value.props"
        />
      </div>
    </EditorLayout>
  </div>
</template>

<script>
import useCms from '../../mixins/useCms';
import EditorLayout from './EditorLayout.vue';
import BlockEditorComponent from './BlockEditorComponent.vue';
import BlockListenersEditor from './BlockListenersEditor.vue';
import CmsBlockComponent from '../../components/CmsBlock/CmsBlockComponent.vue';

import {
  UiIcon,
  UiDataDialog,
  UiInputWedge,
  UiConditionEditor,
} from '/packages/ui/';

export default {
  name: 'CmsBlockEditor',
  mixins: [useCms],
  components: {
    EditorLayout,
    BlockEditorComponent,
    BlockListenersEditor,
    CmsBlockComponent,
    UiConditionEditor,

    UiIcon,
    UiDataDialog,
    UiInputWedge,
  },

  props: {
    value: {
      type: Object,
      required: true,
    },
  },

  data() {
    return {
      options: {},
      definition: {},
      isFocused: false,
    };
  },

  watch: {
    value: {
      immediate: true,
      deep: true,
      async handler(newValue) {
        this.definition = await this.$cms.getDefinition(newValue);
        this.options =
          this.definition && this.definition.editor
            ? this.definition.editor
            : {};
      },
    },
  },
};
</script>