<template>
  <div
    class="cms-page cms-theme"
    :class="value && value.theme ? `--theme-${value.theme}` : null"
  >
    <div class="cms-page-layout">
      <div
        v-for="(row, rowIndex) in layout"
        :key="rowIndex"
        class="page-layout-row"
      >
        <template v-for="(column, columnIndex) in row.columns">
          <div
            v-if="isVisibleColumn(rowIndex, columnIndex)"
            :key="columnIndex"
            class="page-layout-column"
            :style="{flex: column.flex}"
          >
            <CmsBlock
              v-for="(block, i) in column.blocks"
              :key="i"
              class="theme-block"
              :value="block"
              :data.sync="pageModel"
              :vm="vm"
              @update:data="onUpdateData"
              @ref:mounted="onRefMounted"
              @ref:destroyed="onRefDestroyed"
            ></CmsBlock>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
// import '../../themes/default.scss';
import '../../themes/base.scss';

import { VM } from '/packages/vm';
import { getProperty } from '../../functions';
import { validate } from '/packages/validation';

import CmsBlock from '../CmsBlock/CmsBlock.vue';

export default {
  name: 'CmsPage',
  components: { CmsBlock },

  props: {
    value: {
      type: Object,
      required: false,
      default: null,
    },

    model: {
      type: Object,
      required: false,
      default: () => ({}),
    },
  },

  data() {
    return {
      layout: [],
      blockRefs: {},
      pageModel: {},

      watchers: [],
      columnVisibility: [],

      vm: null,
    };
  },

  watch: {
    value: {
      immediate: true,
      handler() {
        this.computeLayout();
        this.setupWatchers();
      },
    },

    model: {
      immediate: true,
      deep: true,
      async handler(newValue) {
        this.pageModel = JSON.parse(JSON.stringify(newValue));
        this.populateColumnVisibility();
      },
    },
  },

  beforeMount() {
    this.vm = new VM();
    this.vm.onModelSet = (varname, value) => this.setVariable(varname, value);

    this.vm.functions.refAction = (args) => {
      if (!args || !args.ref || !args.method) {
        return;
      }

      if (!this.blockRefs[args.ref]) {
        return;
      }

      let ref = this.blockRefs[args.ref];
      if (!ref[args.method]) {
        return;
      }

      let fnArgs = Array.isArray(args.arguments) ? args.arguments : [];
      return ref[args.method](...fnArgs);
    };

    this.runSetup();
  },

  methods: {
    async runSetup() {
      if (!this.value || !this.value.setup) {
        return;
      }

      return await this.vm.eval(this.value.setup, this.pageModel);
    },

    setupWatchers() {
      // clear all existing watchers
      this.watchers.forEach((unregister) => unregister());

      if (
        !this.value ||
        !this.value.watch ||
        !Array.isArray(this.value.watch)
      ) {
        return;
      }

      this.value.watch.forEach((watcher, i) => {
        let watchTargets = Array.isArray(watcher.target)
          ? watcher.target
          : [watcher.target];

        this.watchers[i] = this.$watch(
          () =>
            watchTargets
              .map((varname) => getProperty(this.pageModel, varname))
              .join(),

          async function () {
            if (watcher.expr) {
              try {
                await this.vm.eval(watcher.expr, this.pageModel);
              } catch (err) {
                console.warn('Page.vue@watcher.vm', err);
              }
            }
          },

          {
            immediate: watcher.immediate,
            // deep: watcher.deep, // dado que la propiedad que se esta "watching" es una cadena (.join() ) "deep" no tiene sentido
          }
        );
      });
    },

    setVariable(propertyName, value) {
      let curvar = this.pageModel;
      let parts = propertyName.split('.');
      let len = parts.length;

      for (let i = 0; i < len - 1; i++) {
        if (typeof curvar[parts[i]] == 'undefined' || !curvar[parts[i]]) {
          this.$set(curvar, parts[i], {});
        }
        curvar = curvar[parts[i]];
      }

      this.$set(curvar, parts[len - 1], value);
      this.onUpdateData();
    },

    onUpdateData() {
      this.$emit('update:model', JSON.parse(JSON.stringify(this.pageModel)));
    },

    onRefMounted(event) {
      this.blockRefs[event.name] = event.component;
    },

    onRefDestroyed(event) {
      this.blockRefs[event.name] = undefined;
    },

    computeLayout() {
      if (!this.value) {
        return;
      }

      let page = JSON.parse(JSON.stringify(this.value));
      let layout = page.layout ? page.layout : [];

      // make sure every layout column has a "blocks" array
      layout.forEach((row) => {
        row.columns.forEach((column) => {
          if (!column.blocks) {
            column.blocks = [];
          }
        });
      });

      // make sure the layout contains every row and column specified in block layout
      page.blocks.forEach((block, blockIndex) => {
        block.layout = Object.assign(
          { row: 0, column: 0, order: blockIndex },
          block.layout
        );

        if (!layout[block.layout.row]) {
          layout[block.layout.row] = { columns: [] };
        }

        if (!layout[block.layout.row].columns[block.layout.column]) {
          layout[block.layout.row].columns[block.layout.column] = {
            flex: 1,
            blocks: [],
          };
        }

        if (!layout[block.layout.row].columns[block.layout.column].blocks) {
          layout[block.layout.row].columns[block.layout.column].blocks = [];
        }

        layout[block.layout.row].columns[block.layout.column].blocks.push(
          block
        );
      });

      layout.forEach((row) => {
        row.columns.forEach((column) => {
          column.blocks.sort((a, b) => a.layout.order - b.layout.order);
        });
      });

      this.layout = layout;
    },

    isVisibleColumn(rowIndex, columnIndex) {
      if (
        typeof this.columnVisibility[rowIndex] != 'undefined' &&
        typeof this.columnVisibility[rowIndex][columnIndex] != 'undefined'
      ) {
        return this.columnVisibility[rowIndex][columnIndex];
      }
      return true;
    },

    async populateColumnVisibility() {
      let retval = [];
      for (let r = 0; r < this.layout.length; r++) {
        retval[r] = [];
        for (let c = 0; c < this.layout[r].columns.length; c++) {
          let column = this.layout[r].columns[c];
          if (!column['v-if']) {
            retval[r][c] = true;
          } else {
            retval[r][c] = await validate(this.pageModel, column['v-if']);
          }
        }
      }

      this.columnVisibility = retval;
    },
  },
};
</script>

<style lang="scss">
.cms-page {
  .page-layout-row {
    display: flex;
  }
}

@media (max-width: 800px) {
  .cms-page {
    .page-layout-row {
      display: block;
    }
  }
}
</style>