const UiInput = () => import('/packages/ui/components/UiInput/UiInput.vue')
const InputSettings = () => import('../components/InputSettings/InputSettings.vue')

export default {
  "tags": ["input"],

  "name": "InputFile",
  "title": "Archivo",
  "icon": "mdi:paperclip",

  settings: {
    uploadURL: {
      type: String,
      required: true
    }
  },

  "block": {
    "component": UiInput,
    "v-model": "files",
    "props": {
      "type": "file",
      "path": "{{$settings.uploadURL}}"
    }
  },

  "editor": {
    "actions": [
      {
        "title": "Archivo",
        "component": InputSettings,
        "v-model": "block"
      },
    ]
  }
};